import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

import { zodCountryResourceSchema } from './countries';
import { zodIdSchema } from './general';

export const zodStateSchema = z.object({ code: z.string(), id: zodIdSchema, title: z.string() });

export const zodCityResourceSchema = z
  .object({
    id: zodIdSchema,
    title: z.string(),
    slug: z.string(),
  })
  .merge(
    // partial
    z
      .object({
        country_id: zodIdSchema,
        country: zodCountryResourceSchema,
        state_id: zodIdSchema,
        state: zodStateSchema,
        // 'en-US': { // TODO: https://king-david.atlassian.net/browse/RM-4112
        //   title: 'Tel Aviv',
        // },
      })
      .deepPartial(),
  );

export const zodPopularCityResourceSchema = z
  .object({
    latitude: z.number().optional(),
    longitude: z.number().optional(),
    zoom: z.number({ coerce: true }).optional(), // BE sends zoom as string
  })
  .merge(zodCityResourceSchema);

export const zodCitySchemas = {
  base: zodCityResourceSchema,
  index: z.array(zodCityResourceSchema).default([]),
  show: zodCityResourceSchema,
  popular: z.array(zodPopularCityResourceSchema).default([]),
};

export type CityTypes = TypesFromSchemas<typeof zodCitySchemas>;
