import { ListFilterType } from '@packages/types';
import { get } from 'lodash';
import { FilterAttributeRecord } from 'types/index';

import { parseLabeledFilterValue } from '../parseLabeledFilterValue';
import { parseMultipleLabeledFilterValue } from '../parseMultipleLabeledFilterValue';
import { parseMultipleNumericFilterValue } from '../parseMultipleNumericFilterValue';
import { parseNumericFilterValue } from '../parseNumericFilterValue';
import { parseNumericRangableFilterValue } from '../parseNumericRangableFilterValue';
import { ParsedFilterResult } from '../types';

export interface ParseFilterAttributesFilterValuesParams {
  filter: ListFilterType;
  filterAttributes: Array<FilterAttributeRecord>;
  includeFilterAttribute?: (slug: string) => boolean;
  defaultValues?: Record<string, unknown>;
}

export interface ParseFilterAttributesFilterValuesReturn {
  filters: {
    [key: string]: ParsedFilterResult<unknown, unknown>;
  };
  filterAttributes: ParseFilterAttributesFilterValuesParams['filterAttributes'];
}

export const parseFilterAttributesFilterValues = (
  params: ParseFilterAttributesFilterValuesParams,
): ParseFilterAttributesFilterValuesReturn => {
  const { filter, defaultValues, filterAttributes, includeFilterAttribute } = params;

  const filteredFilterAttributes = includeFilterAttribute
    ? filterAttributes.filter((item) => includeFilterAttribute(item.filter_attribute.slug))
    : filterAttributes;

  const parsedFilters = filteredFilterAttributes.reduce((acc, filterAttribute) => {
    const {
      is_rangable,
      is_multiple,
      filter_attribute: { slug, min, max, step, is_labeled },
    } = filterAttribute;

    const defaultValue = get(defaultValues, slug);

    if (is_rangable) {
      const parsedValue = parseNumericRangableFilterValue({
        slug,
        filter,
        min,
        max,
        step,
        defaultValue,
      });
      return { ...acc, [slug]: parsedValue };
    }

    if (is_multiple) {
      const parsedValue = is_labeled
        ? parseMultipleLabeledFilterValue({ slug, filter, defaultValue })
        : parseMultipleNumericFilterValue({ slug, filter, min, max, step, defaultValue });
      return { ...acc, [slug]: parsedValue };
    }

    if (is_labeled) {
      const parsedValue = parseLabeledFilterValue({ slug, filter, defaultValue });
      return { ...acc, [slug]: parsedValue };
    }

    const parsedValue = parseNumericFilterValue({ filter, slug, min, max, step, defaultValue });

    return { ...acc, [slug]: parsedValue };
  }, {} as ParseFilterAttributesFilterValuesReturn['filters']);

  return {
    filters: parsedFilters,
    filterAttributes: filteredFilterAttributes,
  };
};
