import type { ListParams } from '@packages/types';
import isEmpty from 'lodash/isEmpty';

import type { ParsedUrlQuery } from 'querystring';

export type GetParsedListParamsReturn = ListParams;

export const getParsedListParams = (
  query: ParsedUrlQuery,
  localParams: ListParams,
): GetParsedListParamsReturn => {
  const { perPage, page, sort: sortProp, ...filter } = query;

  let sortField = localParams.sort.field;
  let sortOrder = localParams.sort.order;

  if (typeof sortProp === 'string') {
    sortField = sortProp;
    sortOrder = 'ASC';

    if (sortProp.startsWith('-')) {
      sortField = sortProp.substring(1);
      sortOrder = 'DESC';
    }
  }

  return {
    filter: !isEmpty(filter) ? filter : localParams.filter,
    sort: {
      field: sortField,
      order: sortOrder,
    },
    page: Number(page) || localParams.page,
    perPage: Number(perPage) || localParams.perPage,
  };
};
