import { useEffect, useRef } from 'react';

import cloneDeep from 'lodash/cloneDeep';
import isEqual from 'lodash/isEqual';

export interface UseEffectOnValueChangeParams<ValueType = unknown> {
  value: ValueType;
  callOnInitialisation?: boolean;
  onChange: (params: { prevValue: ValueType; newValue: ValueType }) => void;
}

export const useEffectOnValueChange = <ValueType = unknown>(
  params: UseEffectOnValueChangeParams<ValueType>,
) => {
  const { value, callOnInitialisation = false, onChange } = params;

  const symbol = Symbol();
  const prevValueRef = useRef(
    callOnInitialisation
      ? symbol /* Symbol is used to not be equal to initial value */
      : cloneDeep(value) /* we use cloneDeep to avoid mutating value data */,
  );

  useEffect(() => {
    const prevValue = prevValueRef.current;

    if (!isEqual(prevValue, value)) {
      onChange({ prevValue: prevValue === symbol ? value : prevValue, newValue: value });
    }
    prevValueRef.current = cloneDeep(value);
  }, [
    onChange,
    symbol /* In advance, we need `symbol` to notice `value` change even if it is same link to some object, but object was mutated. It is added to dependency, so useEffect is called on each render */,
    value,
  ]);
};

export default useEffectOnValueChange;
