import { createContext, FC, ReactNode, useContext, useMemo } from 'react';

import { useCurrencyController, UseCurrencyControllerReturn } from 'hooks/useCurrencyController';
import { noop } from 'lodash';

export interface CurrencyContextValue {
  currency: UseCurrencyControllerReturn[0];
  setCurrency: UseCurrencyControllerReturn[1];
}

export interface CookiesContextProviderProps {
  initialCurrencyCode: string | undefined;
  children: ReactNode;
}

export const CurrencyContext = createContext<CurrencyContextValue>({
  currency: undefined,
  setCurrency: noop,
});

export const CurrencyContextProvider: FC<CookiesContextProviderProps> = (props) => {
  const { initialCurrencyCode, children } = props;

  const [currency, setCurrency] = useCurrencyController({
    defaultValue: initialCurrencyCode,
  });

  const value = useMemo(() => {
    return { currency, setCurrency };
  }, [currency, setCurrency]);

  return <CurrencyContext.Provider value={value}>{children}</CurrencyContext.Provider>;
};

export const useCurrencyContext = () => {
  return useContext(CurrencyContext);
};
