import { useCallback, useState } from 'react';

import { ListParams } from '@packages/types';
import { getParsedListParams } from 'helpers/getParsedListParams';
import { cloneDeep, get, isUndefined, mapValues, omit, omitBy, set } from 'lodash';
import { useRouter } from 'next/router';

import type { ParsedUrlQuery } from 'querystring';

import { UseListControllerParams, UseListControllerResult } from './types';

const defaultParams: ListParams = {
  filter: {},
  sort: {},
  page: 1,
  perPage: 15,
};

const toStringOrArrayOfStrings = (value: unknown): string | string[] | undefined => {
  if (!value) return undefined;
  return Array.isArray(value) ? value.map((v) => '' + v) : '' + value;
};

export const useListController = (params?: UseListControllerParams): UseListControllerResult => {
  const { disableSyncWithLocation, ...restParams } = params || {};

  const router = useRouter();

  const [listParams, setListParams] = useState<ListParams>(() => {
    const newParams = {
      ...cloneDeep(defaultParams),
      ...restParams,
    };

    if (disableSyncWithLocation) {
      return newParams;
    }

    return getParsedListParams(router.query, newParams);
  });

  const setPage = useCallback<UseListControllerResult['setPage']>(
    (page, options = {}) => {
      const { replace } = options;

      setListParams((prevParams) => ({ ...prevParams, page }));

      if (disableSyncWithLocation) {
        return;
      }

      router.query.page = String(page);

      const method = replace ? 'replace' : 'push';
      router[method](router, undefined, { shallow: true });
    },
    [disableSyncWithLocation, router],
  );

  const setPerPage = useCallback<UseListControllerResult['setPerPage']>(
    (perPage, options = {}) => {
      const { replace } = options;

      setListParams((prevParams) => ({ ...prevParams, perPage }));

      if (disableSyncWithLocation) {
        return;
      }

      router.query.perPage = String(perPage);

      const method = replace ? 'replace' : 'push';
      router[method](router, undefined, { shallow: true });
    },
    [disableSyncWithLocation, router],
  );

  const setSort = useCallback<UseListControllerResult['setSort']>(
    (sort, options = {}) => {
      const { replace } = options;

      setListParams((prevParams) => ({ ...prevParams, sort }));

      if (disableSyncWithLocation) {
        return;
      }

      if (sort.field) {
        router.query.sort = `${sort.order === 'ASC' ? '' : '-'}${sort.field}`;
      } else {
        router.query = omit(router.query, 'sort');
      }

      const method = replace ? 'replace' : 'push';
      router[method](router, undefined, { shallow: true });
    },
    [disableSyncWithLocation, router],
  );

  const setFilters = useCallback<UseListControllerResult['setFilters']>(
    (callback, options = {}) => {
      const { replace } = options;

      setListParams((prevParams) => {
        let newFilter = callback(prevParams.filter) as ParsedUrlQuery;
        newFilter = omitBy(newFilter, isUndefined);
        newFilter = mapValues(newFilter, toStringOrArrayOfStrings);

        if (!disableSyncWithLocation) {
          const newQuery = cloneDeep(newFilter);

          if (router.query.currency) {
            newQuery.currency = router.query.currency;
          }
          if (router.query.sort) {
            newQuery.sort = router.query.sort;
          }
          if (router.query.perPage) {
            newQuery.perPage = router.query.perPage;
          }
          if (router.query.page) {
            newQuery.page = router.query.page;
          }
          router.query = newQuery;

          const method = replace ? 'replace' : 'push';
          router[method](router, undefined, { shallow: true });
        }

        return { ...prevParams, filter: newFilter };
      });
    },
    [disableSyncWithLocation, router],
  );

  const removeFilter = useCallback<UseListControllerResult['removeFilter']>(
    (name, options = {}) => {
      const { replace } = options;

      setListParams((prevParams) => {
        return omit({ ...prevParams }, `filter.${name}`);
      });

      if (disableSyncWithLocation) {
        return;
      }

      router.query = omit(router.query, name);

      const method = replace ? 'replace' : 'push';
      router[method](router, undefined, { shallow: true });
    },
    [disableSyncWithLocation, router],
  );

  const addFilter = useCallback<UseListControllerResult['addFilter']>(
    (name, value, options = {}) => {
      const { replace } = options;

      if (isUndefined(value)) {
        removeFilter(name, options);
        return;
      }

      const newValue = toStringOrArrayOfStrings(value);

      setListParams((prevParams) => {
        const prevValue = get(prevParams, `filter.${name}`);
        if (prevValue === newValue) {
          return prevParams;
        }
        return set({ ...prevParams }, `filter.${name}`, newValue);
      });

      if (disableSyncWithLocation) {
        return;
      }

      router.query[name] = newValue;

      const method = replace ? 'replace' : 'push';
      router[method](router, undefined, { shallow: true });
    },
    [disableSyncWithLocation, router, removeFilter],
  );

  return {
    listParams,
    setPage,
    setPerPage,
    setSort,
    setFilters,
    addFilter,
    removeFilter,
  };
};
