import { get } from 'lodash';

import {
  getMultipleFilterValueSchema,
  getNumericFilterValueSchema,
  GetNumericFilterValueSchemaParams,
  GetNumericFilterValueSchemaReturn,
  MultipleFilterValueType,
} from '../../schemas';
import { ParsedFilterResult, ParseFilterAttributeParams } from '../types';

type ParseMultipleNumericFilterValueParams<DefaultValue> =
  ParseFilterAttributeParams<DefaultValue> & GetNumericFilterValueSchemaParams;

export type ParseMultipleNumericFilterValueReturn<DefaultValue> = ParsedFilterResult<
  MultipleFilterValueType<GetNumericFilterValueSchemaReturn>,
  DefaultValue
>;

export const parseMultipleNumericFilterValue = <DefaultValue = []>(
  params: ParseMultipleNumericFilterValueParams<DefaultValue>,
): ParseMultipleNumericFilterValueReturn<DefaultValue> => {
  const { slug, filter, min, max, step, defaultValue = [] as DefaultValue } = params;

  const filterValue = get(filter, slug);

  const numericSchema = getNumericFilterValueSchema({ min, max, step });
  const multipleNumericSchema = getMultipleFilterValueSchema(numericSchema);

  const parseResult = multipleNumericSchema.safeParse(filterValue);
  if (parseResult.success) {
    return {
      isEmpty: false,
      value: parseResult.data,
      defaultValue,
    };
  }
  return {
    isEmpty: true,
    value: defaultValue,
    defaultValue,
  };
};
