import { useMemo } from 'react';

import { getSelectNumericChoices } from '@packages/helpers';
import { ACCOMMODATES_SOURCE, PURPOSE_TYPES } from 'application/constants';
import { useGetFilterAttributes } from 'hooks/queries';

export const useGetAccommodatesChoices = () => {
  const { data: filterAttributes = [] } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  const accommodatesChoices = useMemo(() => {
    const accommodatesFilterAttribute = filterAttributes.find(
      (attribute) => attribute.filter_attribute.slug === ACCOMMODATES_SOURCE,
    )?.filter_attribute;

    return getSelectNumericChoices({
      min: accommodatesFilterAttribute?.min || 1,
      max: accommodatesFilterAttribute?.max || 50,
      step: accommodatesFilterAttribute?.step || 1,
    });
  }, [filterAttributes]);

  return accommodatesChoices;
};
