import { z } from 'zod';

import { zodSingleFilterAttributeResourceSchema } from './filter-attributes';
import { zodIdSchema } from './general';

export const zodFormattedFiltersRecordSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        amount: z.string(), // '100'
        amount_max: z.string().optional(), // '100'
        filter_attribute: zodSingleFilterAttributeResourceSchema,
        filter_attribute_id: zodIdSchema,
        measurement_units: z.string(),
        slug: z.string().nullable(),
        synonyms: z.array(z.string()).nullable(),
        title: z.string().nullable(),
      })
      .deepPartial(),
  );

export const zodNavigatorItemSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        title: z.string(),
        path: z.string(),
        order: z.number(),
        target: z.string(),
      })
      .deepPartial(),
  );

export const zodNavigatorSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        placeholder: z.string(),
        items: z.array(zodNavigatorItemSchema),
      })
      .deepPartial(),
  );

export const zodNavigatorSchemas = {
  index: z.array(zodNavigatorSchema).default([]),
  show: zodNavigatorSchema,
};

export const zodCompanySchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        name: z.string(),
        address: z.string(),
        default_currency_id: zodIdSchema,
        default_language_id: zodIdSchema,
        email: z.string(),
        city_id: zodIdSchema,
      })
      .deepPartial(),
  );
