import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

import { zodCityResourceSchema } from './cities';
import { zodFormattedFiltersRecordSchema } from './common';
import { zodCountryResourceSchema } from './countries';
import { zodCurrencySchemas } from './currencies';
import { zodFileSchema } from './files';
import { zodSingleFilterAttributeResourceSchema } from './filter-attributes';
import { zodIdSchema } from './general';

export const zodAttributeSlugSchema = z.literal('amenities');

export const zodFilterRecordSchema = z.record(
  z.union([
    zodIdSchema,
    z.number(),
    z.boolean(), // for value_type as 'boolean': 'is-archived', 'is-published'. We do not use zodBooleanSchema to not transform numbers into boolean
    z.array(zodIdSchema),
    // for range filter
    z.object({ min: z.number().optional(), max: z.number().optional() }),
  ]),
);

export const zodFilterValueResourceSchema = z
  .object({
    id: zodIdSchema, // For labeled it will be uuid, but for numeric it will have the same value as amount
    title: z.string().optional().default(''), // String from translation resource, for numeric it will have the same value as amount
  })
  .merge(
    // partial below
    z
      .object({
        synonyms: z.array(z.string()).nullable(), // For numeric value will be empty
        slug: z.union([zodAttributeSlugSchema, z.string()]), // For labeled it will be string, but for numeric it will have the same value as amount.
        filter_attribute_id: zodIdSchema,
        filter_attribute: zodSingleFilterAttributeResourceSchema,
        amount: z.union([z.number(), zodIdSchema]), // For labeled  it will have the same value as id.
        amount_max: z.number(),
      })
      .deepPartial(),
  );

export const zodListingAgentResourceSchema = z
  .object({
    first_name: z.string().optional().default(''), // postman
    last_name: z.string().optional().default(''), // postman
  })
  .merge(
    z
      .object({
        avatar: zodFileSchema,
      })
      .deepPartial(),
  );

export const zodBaseListingResourceSchema = z
  .object({
    id: zodIdSchema,
    title: z.string().optional().default(''),
    gallery: z.array(zodFileSchema).optional().default([]),
    address: z.string().optional().default(''),
    formatted_filters: z.array(zodFormattedFiltersRecordSchema).optional().default([]),
    currency_id: zodIdSchema.optional(),
    currency: zodCurrencySchemas.base.optional(), // optional because of BE
    slug: z.string(),
  })
  .merge(
    // partial below
    z
      .object({
        filters: zodFilterRecordSchema,
      })
      .deepPartial(),
  );

export const zodShowListingResourceSchema = zodBaseListingResourceSchema.merge(
  z
    .object({
      description: z.record(z.string()).optional(),
      gallery: z.array(zodFileSchema),
      slug: z.string(),
    })
    .merge(
      // partial below
      z
        .object({
          tour_id: z.string(),
          additional_rules: z.string(),
          agent: zodListingAgentResourceSchema,
          city_id: zodIdSchema,
          city: zodCityResourceSchema,
          country_id: zodIdSchema,
          country: zodCountryResourceSchema,
        })
        .deepPartial(),
    ),
);

export const zodListingSchemas = {
  show: zodShowListingResourceSchema,
  index: z.array(zodBaseListingResourceSchema).default([]),
};
export type ListingTypes = TypesFromSchemas<typeof zodListingSchemas>;
