import { isBrowser, normalizeListParams } from '@packages/helpers';
import { ExecuteRecaptcha, ListParams } from '@packages/types';
import axios, { AxiosRequestConfig } from 'axios';
import { getCookie } from 'cookies-next';
import { set } from 'lodash';
import { GetServerSidePropsContext } from 'next';
import qs from 'qs';

import { getWebsiteHost } from './helpers';

const X_FORWARDED_FOR_HEADER = 'x-forwarded-for';
const X_HOST = 'x-host';
const X_LOCALE = 'x-locale';
const X_CURRENCY = 'x-currency';
const RECAPTCHA_TOKEN_KEY = 'g-recaptcha-response';

const hostHeader = getWebsiteHost();

export const baseURL =
  !isBrowser && process.env.USE_HTTP_FOR_SSR_REQUESTS === 'true'
    ? process.env.NEXT_PUBLIC_API_URL?.replace('https:', 'http:')
    : process.env.NEXT_PUBLIC_API_URL;

export const axiosInstance = axios.create({
  baseURL,
  headers: {
    Accept: 'application/json',
    ...(hostHeader && {
      [X_HOST]: hostHeader,
    }),
  },
  paramsSerializer: (params) => {
    return qs.stringify(params, { arrayFormat: 'brackets', skipNulls: true });
  },
});

type RequestConfig = AxiosRequestConfig & { context?: GetServerSidePropsContext };

export const fetchClient = {
  get: (
    params: {
      pathname: string;
      filter?: Record<string, unknown>;
      sort?: ListParams['sort'];
      page?: ListParams['page'];
      perPage?: ListParams['perPage'];
    } & Record<string, unknown>, // analogue of 'any' from Partial<Pick<AxiosRequestConfig, 'params'>>
    config?: RequestConfig,
  ) => {
    const { pathname, filter, page, perPage, sort, ...restFetchParams } = params;

    const { context, ...configRest } = config || {};

    if (context) {
      /**
       * Set forwarded header for BE to correctly detect ip of request sender and not nextjs server
       */
      const xForwardedForValue = context.req.headers[X_FORWARDED_FOR_HEADER];
      if (xForwardedForValue) {
        set(configRest, `headers.${X_FORWARDED_FOR_HEADER}`, xForwardedForValue);
      }

      /**
       * Send x-host header for BE to resolve website
       */
      set(
        configRest,
        `headers.${X_HOST}`,
        getWebsiteHost({
          queryHost: context.query.host as string,
          headerHost: context.req.headers.host,
        }),
      );
      /**
       * Set x-locale header for BE to correctly detect user locale and return coinciding response
       */
      set(configRest, `headers.${X_LOCALE}`, context.locale);

      const currencyCode = getCookie('currency_code', { req: context.req, res: context.res });
      if (currencyCode) {
        /**
         * Set x-currency header for BE to correctly detect user currency and return coinciding response
         */
        set(configRest, `headers.${X_CURRENCY}`, currencyCode);
      }
    }

    const { params: requestParams } = configRest;
    const normalizedListParams = normalizeListParams({ filter, page, perPage, sort });
    const resultParams = { ...requestParams, ...normalizedListParams, ...restFetchParams };
    set(configRest, 'params', resultParams);

    return axiosInstance.get<Record<string, unknown>>(`/${pathname}`, configRest);
  },
  post: async (params: {
    pathname: string;
    data: Record<string, unknown>;
    executeRecaptcha?: ExecuteRecaptcha;
    recaptchaTokenKey?: string;
    config?: RequestConfig;
  }) => {
    const {
      pathname,
      data: payload,
      executeRecaptcha,
      recaptchaTokenKey = RECAPTCHA_TOKEN_KEY,
      config,
    } = params;

    let recaptchaToken;

    try {
      recaptchaToken = await executeRecaptcha?.();
    } catch (error) {
      /**
       * `executeRecaptcha` can throw error, in this case we don't include it
       * to the payload, so we will show the error from our BE
       */
    }

    const data = recaptchaToken ? { ...payload, [recaptchaTokenKey]: recaptchaToken } : payload;

    return axiosInstance.post(`/${pathname}`, data, config);
  },
};

export default fetchClient;
