import { useMemo } from 'react';

import { Paths, PathsFromTranslations } from '@packages/types';
import { useTranslation } from 'next-i18next';
import { TranslationsType } from 'public/locales/types';

export interface UseTranslateParams<BasePath extends string = ''> {
  basePath?: BasePath;
  options?: Record<string, unknown>;
}

export interface UseTranslateReturn<BasePath extends string = ''> {
  (
    path: PathsFromTranslations<TranslationsType, BasePath>,
    options?: Record<string, unknown>,
  ): string;
  /**
   * Use translate.root to get translations ignoring passed basePath (from root)
   */
  root: (path: Paths<TranslationsType>, options?: Record<string, unknown>) => string;
  /**
   * For some dynamic translations as 'string', .e.g. responses from BE etc.
   * It should be used for specific cases. Make typisation for 'path' if it is possible
   */
  try: (path: string, options?: Record<string, unknown>) => string;
}

/**
 * @example
 * const translate = useTranslate({basePath: 'forms.main'})
 *
 * translate('input'); - 'forms.main.input'
 * translate.root('validate.required') - 'validate.required'
 */
export function useTranslate<BasePath extends string = ''>(
  params?: UseTranslateParams<BasePath>,
): UseTranslateReturn<BasePath> {
  const { basePath } = params || {};
  const { t } = useTranslation();

  const translate: UseTranslateReturn<BasePath> = useMemo(() => {
    const _f = ((path, options) => {
      return t(`${basePath ? `${basePath}.` : ''}${path}`, options);
    }) as UseTranslateReturn<BasePath>;

    _f.root = ((path, options) => t(path, options)) as UseTranslateReturn<BasePath>['root'];

    _f.try = _f as unknown as UseTranslateReturn['try'];

    return _f;
  }, [basePath, t]);

  return translate;
}

export default useTranslate;
