import { mapBoundsFilterValuesSchema, MapBoundsFilterValuesType } from '../../schemas';
import { ParsedFilterResult, ParseFilterParams } from '../types';

export type ParseMapBoundsFilterValuesParams<DefaultValue = null> = ParseFilterParams<DefaultValue>;

export type ParseMapBoundsFilterValuesReturn<DefaultValue = null> = ParsedFilterResult<
  MapBoundsFilterValuesType,
  DefaultValue
>;

export const parseMapBoundsFilterValues = <DefaultValue = null>(
  params: ParseMapBoundsFilterValuesParams<DefaultValue>,
): ParseMapBoundsFilterValuesReturn<DefaultValue> => {
  const { filter, defaultValue = null as DefaultValue } = params;

  const parseResult = mapBoundsFilterValuesSchema.safeParse(filter);

  if (parseResult.success) {
    return {
      isEmpty: false,
      value: parseResult.data,
      defaultValue,
    };
  }

  return {
    isEmpty: true,
    value: defaultValue,
    defaultValue,
  };
};
