import { GetPercentValueParams, GetPercentValueReturn } from './types';

const getPercentValue = (params: GetPercentValueParams): GetPercentValueReturn => {
  const { value, maxValue } = params;

  if (value < 0 || maxValue < 0) return 0;
  if (value > maxValue) return 100;

  return (value / maxValue) * 100;
};

export default getPercentValue;
