import { useCallback } from 'react';

import { useCookiesContext } from 'contexts/CookiesContext';

export interface UseCookieParams {
  name: string;
}

type SetCookieType = (value: string) => void;

export type UseCookieReturn = [string | undefined, SetCookieType];

export const useCookie = (params: UseCookieParams): UseCookieReturn => {
  const { name } = params;
  const { cookies, setCookie: setCookieFn } = useCookiesContext();

  const setCookie = useCallback<SetCookieType>(
    (value) => {
      setCookieFn(name, value);
    },
    [setCookieFn, name],
  );

  return [cookies[name], setCookie];
};
