import { MAX_RANGABLE_PREFIX, MIN_RANGABLE_PREFIX } from 'application/constants';
import { get } from 'lodash';

import {
  GetNumericFilterValueSchemaParams,
  getNumericRangableFilterValueSchema,
  NumericRangableFilterValueType,
} from '../../schemas';
import { ParsedFilterResult, ParseFilterAttributeParams } from '../types';

export type ParseNumericRangableFilterValueParams<DefaultValue = [null, null]> =
  ParseFilterAttributeParams<DefaultValue> & GetNumericFilterValueSchemaParams;

export type ParseNumericRangableFilterValueReturn<DefaultValue = [null, null]> = ParsedFilterResult<
  NumericRangableFilterValueType,
  DefaultValue
>;

export const parseNumericRangableFilterValue = <DefaultValue = [null, null]>(
  params: ParseNumericRangableFilterValueParams<DefaultValue>,
): ParseNumericRangableFilterValueReturn<DefaultValue> => {
  const { slug, filter, min, max, step, defaultValue = [null, null] as DefaultValue } = params;

  const numericRangableSchema = getNumericRangableFilterValueSchema({ min, max, step });

  const filterMinValue = get(filter, `${MIN_RANGABLE_PREFIX}${slug}`);
  const filterMaxValue = get(filter, `${MAX_RANGABLE_PREFIX}${slug}`);

  const parseResult = numericRangableSchema.safeParse([filterMinValue, filterMaxValue]);

  if (parseResult.success) {
    return {
      isEmpty: false,
      value: parseResult.data,
      defaultValue,
    };
  }

  return {
    isEmpty: true,
    value: defaultValue,
    defaultValue,
  };
};
