import { get } from 'lodash';

import { LabeledFilterValueType, labeledFilterValueSchema as schema } from '../../schemas';
import { ParsedFilterResult, ParseFilterAttributeParams } from '../types';

export type ParseLabeledFilterValueParams<DefaultValue> = ParseFilterAttributeParams<DefaultValue>;

export type ParseLabeledFilterValueReturn<DefaultValue = ''> = ParsedFilterResult<
  LabeledFilterValueType,
  DefaultValue
>;

export const parseLabeledFilterValue = <DefaultValue = ''>(
  params: ParseLabeledFilterValueParams<DefaultValue>,
): ParseLabeledFilterValueReturn<DefaultValue> => {
  const { slug, filter, defaultValue = '' as DefaultValue } = params;

  const filterValue = get(filter, slug);

  const parseResult = schema.safeParse(filterValue);

  if (parseResult.success) {
    return {
      value: parseResult.data,
      defaultValue,
      isEmpty: false,
    };
  }

  return {
    value: defaultValue,
    defaultValue,
    isEmpty: true,
  };
};
