import { z } from 'zod';

import { zodCountryResourceSchema } from './countries';
import { zodIdSchema } from './general';

export const zodLocalesResourceSchema = z
  .object({
    id: zodIdSchema,
    title: z.string(),
    locale: z.string(),
    direction: z.string(),
    native: z.string(),
  })
  .merge(
    // partial
    z
      .object({
        country_id: zodIdSchema,
        country: zodCountryResourceSchema,
        // 'en-US': { // TODO: https://king-david.atlassian.net/browse/RM-4112
        //   title: 'Tel Aviv',
        // },
      })
      .deepPartial(),
  );

export const zodLocaleSchemas = {
  index: z.array(zodLocalesResourceSchema).default([]),
};
