import { z } from 'zod';

import { zodIdSchema } from './general';

export const LISTING_DRIVER_PAYMENT_METHODS = {
  stripe: 'stripe',
  guestyPay: 'guesty_pay',
} as const;

const driverPaymentMethodBaseSchema = z.object({
  id: zodIdSchema,
  title: z.string(),
  type_id: zodIdSchema,
  type: z.object({
    id: zodIdSchema,
    title: z.string(),
  }),
});

const stripePaymentMethodSchema = z.object({
  driver: z.literal(LISTING_DRIVER_PAYMENT_METHODS['stripe']),
  metadata: z.object({
    setup_payment_intent_key: z.string(),
    public_key: z.string(),
  }),
});

const guestyPayPaymentMethodSchema = z.object({
  driver: z.literal(LISTING_DRIVER_PAYMENT_METHODS['guestyPay']),
  metadata: z.object({
    slug: z.literal('hyp'),
    payment_provider_id: z.string(),
  }),
});

export const zodListingDriverPaymentMethodSchema = z.union([
  driverPaymentMethodBaseSchema.merge(stripePaymentMethodSchema),
  driverPaymentMethodBaseSchema.merge(guestyPayPaymentMethodSchema),
]);
