import { z } from 'zod';

import { zodFormattedFiltersRecordSchema } from './common';
import { zodCurrencySchemas } from './currencies';
import { zodFileSchema } from './files';
import { zodIdSchema } from './general';

export const zodSearchListingDataSchema = z
  .object({
    id: zodIdSchema,
    slug: z.string(),
    formatted_filters: z.array(zodFormattedFiltersRecordSchema),
    title: z.string(),
    gallery: z.array(zodFileSchema),
    currency_id: zodIdSchema,
    currency: zodCurrencySchemas.base,
    address: z.string(),
    match_score: z.number(),
  })
  .merge(
    z
      .object({
        filters: z.unknown(),
        average_price: z.number(),
        thumbnail: z.unknown(),
        missing_amenities: z.array(z.string()),
      })
      .deepPartial(),
  );

export const zodSearchIndexSchema = z.array(zodSearchListingDataSchema);

export const zodSearchSchemas = {
  index: zodSearchIndexSchema,
};
