import { useBuilderState } from '@king-david-it/builder';
import { fetchClient, getWebsiteHost, zodSiteSchemas } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { SITES_RESOURCE } from 'application/resources';
import { AxiosError } from 'axios';
import { GetServerSidePropsContext } from 'next';

type WebsiteDataType = typeof zodSiteSchemas.show._output;

const getWebsite: QueryFunction<WebsiteDataType> = async (params): Promise<WebsiteDataType> => {
  const { meta } = params;
  const endpoint = SITES_RESOURCE;
  const context = meta?.context as GetServerSidePropsContext;

  const host = getWebsiteHost({
    queryHost: context?.query?.host as string,
    headerHost: context?.req?.headers?.host,
  });

  const {
    data: { data },
  } = await fetchClient.get(
    {
      pathname: `${endpoint}?hostname=${host}`,
    },
    {
      context,
    },
  );

  return zodSiteSchemas.show.parse(data);
};

export const fetchWebsite = (queryClient: QueryClient, context: GetServerSidePropsContext) => {
  return queryClient.fetchQuery({
    queryKey: ['sites', 'getOne'],
    queryFn: getWebsite,
    meta: { context },
  });
};

export type UseGetWebsiteParams = {
  enabled?: boolean;
};

export const useGetWebsite = (params: UseGetWebsiteParams = {}) => {
  const { enabled = true } = params;
  const { isEditing } = useBuilderState();

  return useQuery<WebsiteDataType, AxiosError>({
    queryKey: ['sites', 'getOne'],
    queryFn: getWebsite,
    refetchOnMount: false,
    enabled: enabled && !isEditing,
  });
};
