import { getParsedToDateValue } from '@packages/helpers';
import { CHECKIN_AT_SOURCE, CHECKOUT_AT_SOURCE } from 'application/constants';
import { differenceInCalendarDays } from 'date-fns';
import { z, ZodIssueCode } from 'zod';

const dateStringSchema = z.string().superRefine((value, ctx) => {
  if (!value) {
    ctx.addIssue({
      code: ZodIssueCode.too_small,
      minimum: 1,
      inclusive: true,
      type: 'string',
    });
  }

  const parsedDate = getParsedToDateValue({ value, format: 'yyyy-MM-dd' });

  if (!parsedDate) {
    ctx.addIssue({
      code: ZodIssueCode.invalid_date,
    });
  }
});

const checkoutFilterValueSchema = dateStringSchema;

const checkinFilterValueSchema = dateStringSchema.superRefine((value, ctx) => {
  const parsedDate = getParsedToDateValue({ value, format: 'yyyy-MM-dd' });

  if (parsedDate && differenceInCalendarDays(parsedDate, new Date()) < 0) {
    ctx.addIssue({
      code: ZodIssueCode.custom,
      params: { i18n: 'checkin_at_equals_or_after_today' },
    });
  }
});

export const bookingRangeFilterValueSchema = z
  .object({
    [CHECKIN_AT_SOURCE]: checkinFilterValueSchema,
    [CHECKOUT_AT_SOURCE]: checkoutFilterValueSchema,
  })
  .superRefine((values, ctx) => {
    const checkin = values[CHECKIN_AT_SOURCE];
    const checkout = values[CHECKOUT_AT_SOURCE];

    const parsedCheckin = getParsedToDateValue({ value: checkin, format: 'yyyy-MM-dd' });
    const parsedCheckout = getParsedToDateValue({ value: checkout, format: 'yyyy-MM-dd' });

    if (parsedCheckin && parsedCheckout) {
      if (differenceInCalendarDays(parsedCheckout, parsedCheckin) < 0) {
        ctx.addIssue({
          code: ZodIssueCode.custom,
          params: {
            i18n: 'checkout_at_before_checkin_at',
          },
          path: [CHECKOUT_AT_SOURCE],
        });
      }
    }
  });

export type BookingRangeFilterValueSchemaType = typeof bookingRangeFilterValueSchema;
export type BookingRangeFilterValueType = z.output<BookingRangeFilterValueSchemaType>;
