import { format } from 'date-fns';

import { GetFormattedDateValueParams, GetFormattedDateValueReturn } from './types';

import getIsConvertableToDate from '../getIsConvertableToDate';

const getFormattedDateValue = <Fallback = undefined>(
  params: GetFormattedDateValueParams<Fallback>,
): GetFormattedDateValueReturn<Fallback> => {
  const { value, format: saveFormat, fallbackValue } = params;

  const isConvertableToDate = getIsConvertableToDate(value);

  if (value && isConvertableToDate) {
    return format(new Date(value as string | number | Date), saveFormat);
  }

  return fallbackValue as Fallback;
};

export default getFormattedDateValue;
