import { get } from 'lodash';

import {
  getNumericFilterValueSchema,
  GetNumericFilterValueSchemaParams,
  NumericFilterValueType,
} from '../../schemas';
import { ParsedFilterResult, ParseFilterAttributeParams } from '../types';

export type ParseNumericFilterValueParams<DefaultValue> = ParseFilterAttributeParams<DefaultValue> &
  GetNumericFilterValueSchemaParams;

export type ParseNumericFilterValueReturn<DefaultValue> = ParsedFilterResult<
  NumericFilterValueType,
  DefaultValue
>;

export const parseNumericFilterValue = <DefaultValue = ''>(
  params: ParseNumericFilterValueParams<DefaultValue>,
): ParseNumericFilterValueReturn<DefaultValue> => {
  const { slug, filter, min, max, step, defaultValue = '' as DefaultValue } = params;

  const schema = getNumericFilterValueSchema({ min, max, step });

  const filterValue = get(filter, slug);

  const parseResult = schema.safeParse(filterValue);

  if (parseResult.success) {
    return {
      isEmpty: false,
      defaultValue,
      value: parseResult.data,
    };
  }

  return {
    isEmpty: true,
    defaultValue,
    value: defaultValue,
  };
};
