import { useEffectOnValueChange } from './useEffectOnValueChange';
import { useLocalStorage } from './useLocalStorage';

/** We cannot use generic for data, because it is parsed under the hood and returned value may be different */
export const useSyncWithLocalStorage = (params: {
  key: string;
  data: unknown;
  disabled?: boolean;
}): ReturnType<typeof useLocalStorage> => {
  const { key, data, disabled = false } = params;

  const [storageData, setStorageData] = useLocalStorage({
    key,
    fallbackValue: data,
  });

  useEffectOnValueChange({
    value: disabled ? storageData : data, // Use initial local storage if 'disabled'. Helpful when data is not ready yet and we want to use local storage
    onChange: ({ newValue }) => {
      setStorageData(newValue);
    },
    callOnInitialisation: !disabled && !!data, // Here we control local storage overriding by data
  });

  return [storageData, setStorageData];
};

export default useSyncWithLocalStorage;
