import { EAST_SOURCE, NORTH_SOURCE, SOUTH_SOURCE, WEST_SOURCE } from 'application/constants';
import { z } from 'zod';

import { getNumericFilterValueSchema } from './getNumericFilterValueSchema';

export const mapBoundsFilterValuesSchema = z.object({
  [WEST_SOURCE]: getNumericFilterValueSchema({ min: -180, max: 180 }),
  [SOUTH_SOURCE]: getNumericFilterValueSchema({ min: -90, max: 90 }),
  [EAST_SOURCE]: getNumericFilterValueSchema({ min: -180, max: 180 }),
  [NORTH_SOURCE]: getNumericFilterValueSchema({ min: -90, max: 90 }),
});

export type MapBoundsFilterValuesSchemaType = typeof mapBoundsFilterValuesSchema;
export type MapBoundsFilterValuesType = z.output<MapBoundsFilterValuesSchemaType>;
