import { FilterAttributeRecord } from 'types/index';
import { z, ZodIssueCode } from 'zod';

export type GetNumericFilterValueSchemaParams = Pick<
  FilterAttributeRecord['filter_attribute'],
  'min' | 'max' | 'step'
>;

export type GetNumericFilterValueSchemaReturn = ReturnType<typeof getNumericFilterValueSchema>;

export type NumericFilterValueType = z.output<GetNumericFilterValueSchemaReturn>;

export const getNumericFilterValueSchema = (params: GetNumericFilterValueSchemaParams = {}) => {
  const { min, max } = params;

  return (
    z
      .union([z.string(), z.number()])
      // empty strings becomes 0 after coercion, we don't want it
      .superRefine((value, ctx) => {
        if (value === '') {
          ctx.addIssue({
            code: ZodIssueCode.too_small,
            minimum: 1,
            inclusive: true,
            type: 'number',
          });
        }
      })
      .pipe(z.coerce.number())
      .superRefine((value, ctx) => {
        if (!isFinite(value)) {
          ctx.addIssue({
            code: ZodIssueCode.not_finite,
          });
        }
      })
      .pipe(z.number())
      .superRefine((value, ctx) => {
        if (min && value < min) {
          ctx.addIssue({
            code: ZodIssueCode.too_small,
            minimum: min,
            inclusive: true,
            type: 'number',
          });
        }

        if (max && value > max) {
          ctx.addIssue({
            code: ZodIssueCode.too_big,
            maximum: max,
            inclusive: true,
            type: 'number',
          });
        }

        // TODO [RM-6604]: check behavior when price is converted
        // if (step && value % step !== 0) {
        //   ctx.addIssue()
        // }
      })
  );
};
