import { FilterAttributeRecord } from 'types/index';

import { formatLabeledFilterValueToURL } from '../formatLabeledFilterValueToURL';
import { formatMultipleLabeledFilterValueToURL } from '../formatMultipleLabeledFilterValueToURL';
import { formatMultipleNumericFilterValueToURL } from '../formatMultipleNumericFilterValueToURL';
import { formatNumericFilterValueToURL } from '../formatNumericFilterValueToURL';
import { formatNumericRangableFilterValueToURL } from '../formatNumericRangableFilterValueToURL';

export interface FormatFilterAttributesFilterValuesToURLParams {
  values: Record<string, unknown>;
  filterAttributes: Array<FilterAttributeRecord>;
}

export type FormatFilterAttributesFilterValuesToURLReturn = Record<string, string | undefined>;

export const formatFilterAttributesFilterValuesToURL = (
  params: FormatFilterAttributesFilterValuesToURLParams,
): FormatFilterAttributesFilterValuesToURLReturn => {
  const { values, filterAttributes } = params;

  const valuesKeys = Object.keys(values);

  return valuesKeys.reduce((acc, slug) => {
    const filterAttribute = filterAttributes.find((item) => {
      return item.filter_attribute?.slug === slug;
    });

    if (!filterAttribute) return acc;

    const {
      is_rangable,
      is_multiple,
      filter_attribute: { min, max, step, is_labeled },
    } = filterAttribute;

    if (is_rangable) {
      const formattedValue = formatNumericRangableFilterValueToURL({
        slug,
        values,
        min,
        max,
        step,
      });

      return { ...acc, ...formattedValue };
    }

    if (is_multiple) {
      const formattedValue = is_labeled
        ? formatMultipleLabeledFilterValueToURL({ slug, values })
        : formatMultipleNumericFilterValueToURL({ slug, values, min, max, step });

      return { ...acc, [slug]: formattedValue };
    }

    if (is_labeled) {
      const formattedValue = formatLabeledFilterValueToURL({ slug, values });
      return { ...acc, [slug]: formattedValue };
    }

    const formattedValue = formatNumericFilterValueToURL({ slug, values, min, max, step });

    return { ...acc, [slug]: formattedValue };
  }, {} as FormatFilterAttributesFilterValuesToURLReturn);
};
