import { fetchClient, ListingTypes, zodListingSchemas } from '@packages/providers';
import { Identifier } from '@packages/types';
import { useQuery } from '@tanstack/react-query';
import { LISTINGS_RESOURCE } from 'application/resources';

const getQueryFn = (params: { ids: Identifier[] }) => async (): Promise<ListingTypes['index']> => {
  const { ids } = params;

  const {
    data: { data },
  } = await fetchClient.get({ pathname: LISTINGS_RESOURCE, filter: { ids } });

  return zodListingSchemas.index.parse(data);
};

export const useGetListings = (params: { ids: Identifier[] }) => {
  const { ids } = params;

  return useQuery({
    queryKey: [LISTINGS_RESOURCE, params],
    queryFn: getQueryFn({ ids }),
    enabled: true,
    refetchOnMount: false,
  });
};

export default useGetListings;
