import { z } from 'zod';

import { zodCurrencySchemas } from './currencies';
import { zodBooleanSchema, zodDateSchema, zodIdSchema } from './general';

export const zodAttributeValueTypeSchema = z.enum([
  'label',
  'boolean',
  'numeric',
  'price',
  'area',
  'mass',
  'length',
  'capacity',
  'tag',
  'city',
  'owner',
  'unit_type',
  'deal_type',
  'unit',
  'cancellation_policy',
  'company',
  'geo_point',
]);

export const zodSingleFilterAttributeResourceSchema = z
  .object({
    id: zodIdSchema,
    title: z.string(),
    slug: z.string(),
  })
  .merge(
    z
      .object({
        measurements_level: z.number(),
        rank: z.number(),
        value_type: z.union([z.string(), zodAttributeValueTypeSchema]),
        created_at: zodDateSchema.nullable(),
        updated_at: zodDateSchema.nullable(),
        is_default: zodBooleanSchema,
        default: z.string(),
        /**
         * Nullable only for labeled FA, needed for validation
         */
        min: z.number().nullable(),
        max: z.number().nullable(),
        step: z.number().nullable(), // Nullable only for labeled FA
        is_labeled: zodBooleanSchema,
        is_system: zodBooleanSchema,
        currency: zodCurrencySchemas.base,
      })
      .deepPartial(),
  );

export const zodFilterAttributeResourceSchema = z
  .object({
    id: zodIdSchema,
    filter_attribute: zodSingleFilterAttributeResourceSchema,
    filter_attribute_id: zodIdSchema,
  })
  .merge(
    z
      .object({
        created_at: zodDateSchema.nullable(),
        updated_at: zodDateSchema.nullable(),
        is_multiple: zodBooleanSchema,
        is_rangable: zodBooleanSchema,
        is_required: zodBooleanSchema,
        order: z.number(),
        typeable: z.object({
          id: zodIdSchema,
          title: z.string(),
          slug: z.string(),
        }),
        typeable_id: zodIdSchema,
        typeable_type: z.string(),
      })
      .deepPartial(),
  );

export const zodFilterLabelsResourceSchema = z
  .object({
    id: zodIdSchema,
    filter_attribute: zodSingleFilterAttributeResourceSchema,
    title: z.string(),
    slug: z.string(),
  })
  .merge(
    z
      .object({
        synonyms: z.array(z.string()),
        measurement_units: z.string(),
        rank: z.number(),
      })
      .deepPartial(),
  );

export const zodFilterAttributeResourceIndexSchema = z.array(zodFilterAttributeResourceSchema);

export const zodFilterLabelsResourceIndexSchema = z.array(zodFilterLabelsResourceSchema);
