import { fetchClient, zodDealTypesIndexSchema } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { DEAL_TYPES_RESOURCE } from 'application/resources';
import { GetServerSidePropsContext } from 'next';
import { DealTypeRecord } from 'types/index';

const getQueryKey = () => [DEAL_TYPES_RESOURCE];

const queryFn: QueryFunction<DealTypeRecord[]> = async (params) => {
  const { meta } = params;
  const context = meta?.context as GetServerSidePropsContext;

  const {
    data: { data },
  } = await fetchClient.get(
    {
      pathname: DEAL_TYPES_RESOURCE,
    },
    { context },
  );

  return zodDealTypesIndexSchema.parse(data);
};

export const prefetchDealTypes = (queryClient: QueryClient, context: GetServerSidePropsContext) => {
  return queryClient.prefetchQuery({
    queryKey: getQueryKey(),
    queryFn,
    meta: { context },
  });
};

export const fetchDealTypes = (queryClient: QueryClient, context?: GetServerSidePropsContext) => {
  return queryClient.fetchQuery({
    queryKey: getQueryKey(),
    queryFn,
    meta: { context },
  });
};

export const useGetDealTypes = () => {
  return useQuery({
    queryKey: getQueryKey(),
    queryFn,
    refetchOnMount: false,
  });
};
