import { useCallback, useRef } from 'react';

export interface UseIntersectionObserverParams extends IntersectionObserverInit {
  isEnabled?: boolean;
  onIntersection?: () => void;
}

/**
 * Detects when element becomes visible on screen.
 * Usage: attach targetElementRef to observed element
 */
export const useIntersectionObserver = (params: UseIntersectionObserverParams) => {
  const { isEnabled, onIntersection, root, rootMargin, threshold } = params;

  const observer = useRef<IntersectionObserver>();

  const targetElementRef = useCallback(
    (node: HTMLElement | null) => {
      if (!isEnabled) return;

      if (observer.current) observer.current.disconnect();

      observer.current = new IntersectionObserver(
        (entries) => {
          const [firstEntry] = entries;

          if (firstEntry?.isIntersecting && isEnabled && onIntersection) {
            onIntersection();
          }
        },
        { root, rootMargin, threshold },
      );

      if (node) observer.current.observe(node);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [isEnabled, onIntersection, JSON.stringify(root), rootMargin, threshold],
  );

  return { targetElementRef };
};
