import { POLYGON_FILTER_SOURCE } from 'application/constants';
import { ParseMapBoundsFilterValuesReturn } from 'helpers/filters';

export type GetPolygonFilterFromBoundsParams = {
  bounds: ParseMapBoundsFilterValuesReturn['value'];
};

export type GetPolygonFilterFromBoundsReturn =
  | {
      [POLYGON_FILTER_SOURCE]: {
        0: [number, number]; // [west, south]
        1: [number, number]; // [east, south]
        2: [number, number]; // [east, north]
        3: [number, number]; // [west, north]
        4: [number, number]; // [west, south] (Note: The 4th entry duplicated with the first to "close" the polygon)
      };
    }
  | undefined;

export const getPolygonFilterFromBounds = (
  params: GetPolygonFilterFromBoundsParams,
): GetPolygonFilterFromBoundsReturn => {
  const { bounds } = params;

  if (!bounds) return undefined;

  const { east, north, south, west } = bounds;
  return {
    // hack for BE to distinguish because we can't flexibly customize query-params
    [POLYGON_FILTER_SOURCE]: {
      0: [west, south],
      1: [east, south],
      2: [east, north],
      3: [west, north],
      4: [west, south],
    },
  };
};
