import { fetchClient, zodCurrencySchemas } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { CURRENCIES_RESOURCE } from 'application/resources';
import { GetServerSidePropsContext } from 'next';

const queryKey = [CURRENCIES_RESOURCE] as const;

const queryFn: QueryFunction<typeof zodCurrencySchemas.index._output> = async (params) => {
  const { meta } = params;
  const context = meta?.context as GetServerSidePropsContext;
  const endpoint = CURRENCIES_RESOURCE;

  const {
    data: { data },
  } = await fetchClient.get(
    {
      pathname: endpoint,
    },
    {
      context,
    },
  );

  return zodCurrencySchemas.index.parse(data);
};

export const fetchCurrencies = (queryClient: QueryClient, context: GetServerSidePropsContext) => {
  return queryClient.fetchQuery({
    queryKey,
    queryFn,
    meta: { context },
  });
};

export const prefetchCurrencies = (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  return queryClient.prefetchQuery({
    queryKey,
    queryFn,
    meta: { context },
  });
};

export const useGetCurrencies = () => {
  return useQuery({
    queryKey,
    queryFn,
    refetchOnMount: false,
    enabled: true,
  });
};
