import { useBuilderState } from '@king-david-it/builder';
import { fetchClient, zodPageSchema } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { STATIC_PAGES_PATHES } from 'application/constants';
import { GetServerSidePropsContext } from 'next';
import { useRouter } from 'next/router';
import { PageRecord } from 'types/index';

const HOME_PAGE = 'home';

type PageQueryKey = readonly [string, string, { path: string }];

const getQueryKey = (path?: string): PageQueryKey => [
  'page',
  'getOne',
  { path: path || HOME_PAGE },
];

const queryFn: QueryFunction<PageRecord, PageQueryKey> = async (params) => {
  const { queryKey, meta } = params;

  const path = queryKey[2] && queryKey[2].path;

  const context = meta?.context as GetServerSidePropsContext;

  const res = await fetchClient.get({ pathname: `pages/${path}` }, { context });

  return zodPageSchema.parse(res.data.data);
};

export const prefetchPage = (
  queryClient: QueryClient,
  context?: GetServerSidePropsContext,
  params?: { path: string },
) => {
  let pathname = context?.resolvedUrl.split('?')[0]?.slice(1) || params?.path;

  // For some reason for home page resolvedUrl becomes `/undefined`
  if (pathname === 'undefined') {
    pathname = undefined;
  }

  return queryClient.prefetchQuery({
    queryKey: getQueryKey(pathname),
    queryFn,
    meta: { context },
  });
};

export const useGetPage = () => {
  const { asPath, pathname } = useRouter();

  const realPath = asPath?.split('?')[0]?.slice(1) || HOME_PAGE;

  const { isEditing } = useBuilderState();

  const isStaticPage = STATIC_PAGES_PATHES.includes(pathname);

  return useQuery({
    queryKey: getQueryKey(realPath),
    queryFn,
    refetchOnMount: false,
    enabled: !isEditing && !isStaticPage,
  });
};
