import { z } from 'zod';

import {
  getNumericFilterValueSchema,
  GetNumericFilterValueSchemaParams,
} from '../getNumericFilterValueSchema';
import { getRangableFilterValueSchema } from '../getRangableFilterValueSchema';

export type GetNumericRangableFilterValueSchemaParams = GetNumericFilterValueSchemaParams;

export type GetNumericRangableFilterValueSchemaReturn = ReturnType<
  typeof getNumericRangableFilterValueSchema
>;

export type NumericRangableFilterValueType = z.output<GetNumericRangableFilterValueSchemaReturn>;

export const getNumericRangableFilterValueSchema = (params: GetNumericFilterValueSchemaParams) => {
  const { min, max, step } = params;

  const numericSchema = getNumericFilterValueSchema({ min, max, step });

  return getRangableFilterValueSchema(numericSchema.nullish())
    .transform(([min, max]): [number | null, number | null] => {
      const newMin = typeof min === 'undefined' ? null : min;
      const newMax = typeof max === 'undefined' ? null : max;
      return [newMin, newMax];
    })
    .refine(([min, max]) => {
      if (typeof min === 'number' && typeof max === 'number') {
        return min <= max;
      }

      return typeof min === 'number' || typeof max === 'number';
    });
};
