/* eslint-disable no-console */
import { Builder, BuilderStateContextType } from '@king-david-it/builder';
import { QueryClient } from '@tanstack/react-query';
import axios from 'axios';
import get from 'lodash/get';
import { GetServerSidePropsContext } from 'next';

export interface CreateQueryClientParams {
  context?: GetServerSidePropsContext;
  builderState?: BuilderStateContextType;
}

export const createQueryClient = (params?: CreateQueryClientParams) => {
  const { context, builderState } = params || {};

  const { isEditing } = builderState || Builder.getStateFromURL(context?.req?.url);

  return new QueryClient({
    defaultOptions: {
      queries: {
        enabled: !isEditing,
        refetchOnWindowFocus: true,
        retry: false,
        // Prevent additional api calls by increasing data stale time
        staleTime: 10 * 60 * 1000,
        cacheTime: 20 * 60 * 1000,
        meta: {
          // pass context in meta to all queries
          context,
        },
      },
    },
    logger: {
      log: console.log,
      warn: console.warn,
      error: (...args) => {
        const [error] = args;
        if (axios.isAxiosError(error)) {
          console.error('Axios error', {
            status: error.response?.status,
            message: get(error, 'response.data.message'),
            host: error.request?.host,
            rawHeaders: error.request?._header,
            headers: error.config.headers,
            baseURL: error.config.baseURL,
            url: error.config.url,
          });
        } else {
          console.error(...args);
        }
      },
    },
  });
};
