import { useCallback, useEffect, useState } from 'react';

import { useQueryClient } from '@tanstack/react-query';
import { CURRENCY_COOKIE_KEY } from 'application/cookiesKeys';
import { useCookie, useGetCurrencies, usePrevious } from 'hooks/index';
import Router from 'next/router';
import { CurrencyRecord } from 'types';

export const CURRENCY_QUERY_PARAM_KEY = 'currency';

type SelectedCurrency = CurrencyRecord | undefined;
type SetSelectedCurrency = (code: string) => void;

export type UseCurrencyControllerParams = {
  defaultValue: string | undefined;
};

export type UseCurrencyControllerReturn = [SelectedCurrency, SetSelectedCurrency];

export const useCurrencyController = (
  params: UseCurrencyControllerParams,
): UseCurrencyControllerReturn => {
  const { defaultValue } = params;

  const { data: currenciesData = [] } = useGetCurrencies();

  const queryClient = useQueryClient();

  const [, setCookieCurrencyCode] = useCookie({
    name: CURRENCY_COOKIE_KEY,
  });

  const [selectedCurrencyCode, setSelectedCurrencyCode] = useState(defaultValue);
  const prevCurrencyCode = usePrevious(selectedCurrencyCode);

  // update currency in the URL after currency change
  useEffect(() => {
    if (selectedCurrencyCode && prevCurrencyCode !== selectedCurrencyCode) {
      Router.replace(
        {
          pathname: Router.pathname,
          query: { ...Router.query, [CURRENCY_QUERY_PARAM_KEY]: selectedCurrencyCode },
        },
        undefined,
        { shallow: true },
      );
    }
  }, [selectedCurrencyCode, prevCurrencyCode]);

  // update currency in cookies after currency change
  useEffect(() => {
    if (selectedCurrencyCode) {
      setCookieCurrencyCode(selectedCurrencyCode);
    }
  }, [selectedCurrencyCode, setCookieCurrencyCode]);

  // refetch all requests after currency change
  useEffect(() => {
    if (selectedCurrencyCode && selectedCurrencyCode !== prevCurrencyCode) {
      queryClient.invalidateQueries();
    }
  }, [prevCurrencyCode, queryClient, selectedCurrencyCode]);

  const selectedCurrency = currenciesData.find((currency) => {
    return currency.code === selectedCurrencyCode;
  });

  const setSelectedCurrency = useCallback((code: string) => {
    setSelectedCurrencyCode(code);
  }, []);

  return [selectedCurrency, setSelectedCurrency];
};
