import { ListFilterType } from '@packages/types';
import {
  BOOKING_RANGE_SOURCE,
  BOUNDS_SOURCE,
  COUNTRY_SOURCE,
  STATE_SOURCE,
} from 'application/constants';
import { get } from 'lodash';

import { INDEPENDENT_FILTERS_SLUGS } from '../../constants';
import { IndependentFiltersSlugs } from '../../types';
import {
  parseBookingRangeFilterValue,
  ParseBookingRangeFilterValueReturn,
} from '../parseBookingRangeFilterValue';
import { parseCountryFilterValue, ParseCountryFilterValueReturn } from '../parseCountryFilterValue';
import {
  parseMapBoundsFilterValues,
  ParseMapBoundsFilterValuesReturn,
} from '../parseMapBoundsFilterValues';
import { parseStateFilterValue, ParseStateFilterValueReturn } from '../parseStateFilterValue';

export type IndependentFilters<
  DefaultValues extends Partial<Record<IndependentFiltersSlugs[number], unknown>>,
> = {
  [BOUNDS_SOURCE]: ParseMapBoundsFilterValuesReturn<DefaultValues[typeof BOUNDS_SOURCE]>;
  [BOOKING_RANGE_SOURCE]: ParseBookingRangeFilterValueReturn<
    DefaultValues[typeof BOOKING_RANGE_SOURCE]
  >;
  [STATE_SOURCE]: ParseStateFilterValueReturn<DefaultValues[typeof STATE_SOURCE]>;
  [COUNTRY_SOURCE]: ParseCountryFilterValueReturn<DefaultValues[typeof COUNTRY_SOURCE]>;
};

export type IndependentFiltersDefaultValues = {
  [BOUNDS_SOURCE]: ParseMapBoundsFilterValuesReturn['defaultValue'];
  [BOOKING_RANGE_SOURCE]: ParseBookingRangeFilterValueReturn['defaultValue'];
  [STATE_SOURCE]: ParseStateFilterValueReturn['defaultValue'];
  [COUNTRY_SOURCE]: ParseCountryFilterValueReturn['defaultValue'];
};

export interface ParseIndependentFilterValuesParams<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> {
  filter: ListFilterType;
  slugs?: Slugs;
  defaultValues?: DefaultValues;
}

// Return only filters that are listed in `slugs` array
export type ParseIndependentFilterValuesReturn<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  filters: {
    [Key in Slugs[number]]: Key extends keyof DefaultValues
      ? IndependentFilters<DefaultValues>[Key]
      : IndependentFilters<IndependentFiltersDefaultValues>[Key];
  };
};

export const parseIndependentFilterValues = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
>(
  params: ParseIndependentFilterValuesParams<Slugs, DefaultValues>,
): ParseIndependentFilterValuesReturn<Slugs, DefaultValues> => {
  const { filter, slugs = INDEPENDENT_FILTERS_SLUGS, defaultValues } = params;

  const parsedFilters = slugs.reduce((acc, slug) => {
    const defaultValue = get(defaultValues, slug);

    switch (slug) {
      case BOOKING_RANGE_SOURCE: {
        return { ...acc, [slug]: parseBookingRangeFilterValue({ filter, defaultValue }) };
      }
      case BOUNDS_SOURCE:
        return { ...acc, [slug]: parseMapBoundsFilterValues({ filter, defaultValue }) };
      case COUNTRY_SOURCE:
        return { ...acc, [slug]: parseCountryFilterValue({ filter, slug, defaultValue }) };
      case STATE_SOURCE:
        return { ...acc, [slug]: parseStateFilterValue({ filter, slug, defaultValue }) };
      default:
        return acc;
    }
  }, {} as ParseIndependentFilterValuesReturn<Slugs, DefaultValues>['filters']);

  return {
    filters: parsedFilters,
  };
};
