import { get } from 'lodash';

import {
  getMultipleFilterValueSchema,
  LabeledFilterValueSchemaType,
  labeledFilterValueSchema as labeledSchema,
  MultipleFilterValueType,
} from '../../schemas';
import { ParsedFilterResult, ParseFilterAttributeParams } from '../types';

type ParseMultipleLabeledFilterValueParams<DefaultValue> = ParseFilterAttributeParams<DefaultValue>;

export type ParseMultipleLabeledFilterValueReturn<DefaultValue> = ParsedFilterResult<
  MultipleFilterValueType<LabeledFilterValueSchemaType>,
  DefaultValue
>;

export const parseMultipleLabeledFilterValue = <DefaultValue = []>(
  params: ParseMultipleLabeledFilterValueParams<DefaultValue>,
): ParseMultipleLabeledFilterValueReturn<DefaultValue> => {
  const { slug, filter, defaultValue = [] as DefaultValue } = params;

  const filterValue = get(filter, slug);

  const multipleLabeledSchema = getMultipleFilterValueSchema(labeledSchema);

  const parseResult = multipleLabeledSchema.safeParse(filterValue);
  if (parseResult.success) {
    return {
      isEmpty: false,
      value: parseResult.data,
      defaultValue,
    };
  }
  return {
    isEmpty: true,
    value: defaultValue,
    defaultValue,
  };
};
