import { format, isValid } from 'date-fns';

import { GetFormattedDateAndTimeReturn, GetFormattedDateAnTimeParams } from './types';

const getFormattedDateAndTime = (
  params: GetFormattedDateAnTimeParams,
): GetFormattedDateAndTimeReturn => {
  const { date, dateFormat, timeFormat, dateVariant = 'default' } = params;

  /* https://date-fns.org/v2.27.0/docs/format */
  const dateFormats: Record<NonNullable<GetFormattedDateAnTimeParams['dateVariant']>, string> = {
    long: 'PP',
    full: 'PPP',
    default: 'yyyy-MM-dd',
  };
  const defaultDateFormat = dateFormats[dateVariant] || dateFormats.default;
  const defaultTimeFormat = 'HH:mm';

  return {
    date: isValid(date) ? format(date, dateFormat || defaultDateFormat) : '',
    time: isValid(date) ? format(date, timeFormat || defaultTimeFormat) : '',
  };
};

export default getFormattedDateAndTime;
