import { createContext, FC, ReactNode, useContext } from 'react';

import {
  useListController,
  UseListControllerParams,
  UseListControllerResult,
} from 'hooks/useListController';
import noop from 'lodash/noop';

export type ListContextValue = UseListControllerResult;

const defaultListContextValue: ListContextValue = {
  listParams: { filter: {}, sort: {}, page: 1, perPage: 15 },
  setPage: noop,
  setPerPage: noop,
  setSort: noop,
  setFilters: noop,
  addFilter: noop,
  removeFilter: noop,
};

export const ListContext = createContext<ListContextValue>(defaultListContextValue);

export interface ListContextProviderProps extends UseListControllerParams {
  children: ReactNode;
}

export const ListContextProvider: FC<ListContextProviderProps> = ({ children, ...rest }) => {
  return <ListContext.Provider value={useListController(rest)}>{children}</ListContext.Provider>;
};

export const useListContext = () => {
  return useContext(ListContext);
};

export default ListContextProvider;
