import { z, ZodTypeAny } from 'zod';

export type GetRangableFilterValueSchemaReturn<Schema extends ZodTypeAny> = ReturnType<
  typeof getRangableFilterValueSchema<Schema>
>;

export type RangableFilterValueType<Schema extends ZodTypeAny> = z.output<
  GetRangableFilterValueSchemaReturn<Schema>
>;

export const getRangableFilterValueSchema = <Schema extends ZodTypeAny>(schema: Schema) => {
  return z.tuple([schema, schema]);
};
