/**
 * ---------- Routes -----------
 */

const LISTINGS_ROUTE = 'listings';
const LISTING_ROUTE_PARAM = 'slug' as const; // must be the same as pages/listings/[slug].tsx
export const listingRoute = {
  route: LISTINGS_ROUTE,
  param: LISTING_ROUTE_PARAM,
  pathname: `/${LISTINGS_ROUTE}/${LISTING_ROUTE_PARAM}`,
  getPathname: (slug: string) => `/${LISTINGS_ROUTE}/${slug}`,
};

export const EMBEDDED_ROUTE = 'embed';
export const SEARCH_ROUTE = 's';

export const RESERVATION_ROUTE = 'r';
const RESERVATION_ROUTE_PARAM = 'slug' as const; // must be the same as pages/r/[slug].tsx
export const reservationRoute = {
  route: RESERVATION_ROUTE,
  param: RESERVATION_ROUTE_PARAM,
  pathname: `/${RESERVATION_ROUTE}/${RESERVATION_ROUTE_PARAM}`,
  getPathname: (slug: string) => `/${RESERVATION_ROUTE}/${slug}`,
};

export const THANK_YOU_ROUTE = 'thank-you';
const THANK_YOU_ROUTE_PARAM = 'reference' as const; // must be the same as pages/r/thank-you/[reference].tsx
export const thankYouRoute = {
  route: THANK_YOU_ROUTE,
  param: THANK_YOU_ROUTE_PARAM,
  pathname: `/${RESERVATION_ROUTE}/${THANK_YOU_ROUTE}/${THANK_YOU_ROUTE_PARAM}`,
  getPathname: (reference: string) => `/${RESERVATION_ROUTE}/${THANK_YOU_ROUTE}/${reference}`,
};

/**
 * We need this const to define static pages, used in useGetPage.
 * EXAMPLE: Do not send requests to '/pages' if the page is not from the builder
 */
export const STATIC_PAGES_PATHES = [
  listingRoute.pathname,
  reservationRoute.pathname,
  thankYouRoute.pathname,
];

