import { parseNumericFilterValue } from 'helpers/filters/parsers';

import { GetNumericFilterValueSchemaParams } from '../../schemas/getNumericFilterValueSchema';
import { FormatFilterAttributeToURLParams } from '../types';

export type FormatNumericFilterValueToURLParams = FormatFilterAttributeToURLParams &
  GetNumericFilterValueSchemaParams;

export type FormatNumericFilterValueToURLReturn = string | undefined;

export const formatNumericFilterValueToURL = (
  params: FormatNumericFilterValueToURLParams,
): FormatNumericFilterValueToURLReturn => {
  const { values, slug, min, max, step } = params;

  const parseResult = parseNumericFilterValue({
    filter: values,
    slug,
    min,
    max,
    step,
  });

  if (parseResult.isEmpty) {
    return undefined;
  }

  return String(parseResult.value);
};
