import { IndependentFiltersDefaultValues, ParseFilterValuesReturn } from '../parsers';
import { IndependentFiltersSlugs } from '../types';

export type SplitParsedFilterValuesParams<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  filters: ParseFilterValuesReturn<Slugs, DefaultValues>['filters'];
};

type AllFilters<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  [Key in keyof ParseFilterValuesReturn<Slugs, DefaultValues>['filters']]: ParseFilterValuesReturn<
    Slugs,
    DefaultValues
  >['filters'][Key] extends { value: infer V }
    ? V
    : never;
};

type SelectedFilters<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  [Key in keyof ParseFilterValuesReturn<Slugs, DefaultValues>['filters']]?: Extract<
    ParseFilterValuesReturn<Slugs, DefaultValues>['filters'][Key],
    { isEmpty: false }
  >['value'];
};

type EmptyFilters<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  [Key in keyof ParseFilterValuesReturn<Slugs, DefaultValues>['filters']]?: Extract<
    ParseFilterValuesReturn<Slugs, DefaultValues>['filters'][Key],
    { isEmpty: true }
  >['value'];
};

type DefaultFilterValues<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  [Key in keyof ParseFilterValuesReturn<Slugs, DefaultValues>['filters']]: ParseFilterValuesReturn<
    Slugs,
    DefaultValues
  >['filters'][Key] extends { defaultValue: infer V }
    ? V
    : never;
};

export type SplitParsedFilterValuesReturn<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Record<Slugs[number], unknown>,
> = {
  allFilters: AllFilters<Slugs, DefaultValues>;
  selectedFilters: SelectedFilters<Slugs, DefaultValues>;
  emptyFilters: EmptyFilters<Slugs, DefaultValues>;
  defaultValues: DefaultFilterValues<Slugs, DefaultValues>;
};

export const splitParsedFilterValues = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
>(
  params: SplitParsedFilterValuesParams<Slugs, DefaultValues>,
): SplitParsedFilterValuesReturn<Slugs, DefaultValues> => {
  const { filters } = params;

  const slugs = Object.keys(filters) as Array<keyof AllFilters<Slugs, DefaultValues>>;

  return slugs.reduce(
    (acc, slug) => {
      const parsedValue = filters[slug];

      const { isEmpty, value, defaultValue } = parsedValue;

      acc.defaultValues[slug] = defaultValue as DefaultFilterValues<
        Slugs,
        DefaultValues
      >[keyof DefaultFilterValues<Slugs, DefaultValues>];

      acc.allFilters[slug] = value as AllFilters<Slugs, DefaultValues>[keyof AllFilters<
        Slugs,
        DefaultValues
      >];

      if (isEmpty) {
        acc.emptyFilters[slug] = defaultValue as EmptyFilters<
          Slugs,
          DefaultValues
        >[keyof EmptyFilters<Slugs, DefaultValues>];
      } else {
        acc.selectedFilters[slug] = value as SelectedFilters<
          Slugs,
          DefaultValues
        >[keyof SelectedFilters<Slugs, DefaultValues>];
      }

      return acc;
    },
    {
      allFilters: {},
      emptyFilters: {},
      selectedFilters: {},
      defaultValues: {},
    } as SplitParsedFilterValuesReturn<Slugs, DefaultValues>,
  );
};
