import { fetchClient, zodFilterLabelsResourceIndexSchema } from '@packages/providers';
import { Sort } from '@packages/types';
import { QueryClient, useQuery } from '@tanstack/react-query';
import { AMENITIES_SOURCE } from 'application/constants';
import { GetServerSidePropsContext } from 'next';
import { FilterLabelsRecord } from 'types';

const sort: Sort = { field: 'rank,title', order: 'DESC' };
const fetchParams = { all: 1 };
const getQueryKey = () => [AMENITIES_SOURCE, sort, fetchParams];

const getAmenities = async (context?: GetServerSidePropsContext): Promise<FilterLabelsRecord[]> => {
  const {
    data: { data },
  } = await fetchClient.get(
    {
      pathname: AMENITIES_SOURCE,
      sort,
      ...fetchParams,
    },
    { context },
  );

  return zodFilterLabelsResourceIndexSchema.parse(data);
};

export const prefetchAmenities = async (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  return queryClient.prefetchQuery({
    // eslint-disable-next-line @tanstack/query/exhaustive-deps -- "context" is not needed here?!
    queryKey: getQueryKey(),
    queryFn: () => getAmenities(context),
    meta: { context },
  });
};

export const useGetAmenities = () => {
  return useQuery({
    queryKey: getQueryKey(),
    queryFn: () => getAmenities(),
    refetchOnMount: false,
  });
};
