import { z } from 'zod';

import { zodIdSchema, zodNumberSchema } from './general';

const zodCurrencySchema = z
  .object({
    id: zodIdSchema,
    sign: z.string(),
    code: z.string(),
    title: z.string(),
  })
  .merge(
    z
      .object({
        rate: zodNumberSchema,
      })
      .deepPartial(),
  );

export const zodCurrencySchemas = {
  base: zodCurrencySchema,
  index: z.array(zodCurrencySchema).default([]),
};
