import {
  fetchClient,
  ListResponseType,
  zodListResponseSchema,
  zodSearchSchemas,
} from '@packages/providers';
import { ListParams } from '@packages/types';
import {
  QueryClient,
  QueryFunction,
  useInfiniteQuery,
  UseInfiniteQueryOptions,
} from '@tanstack/react-query';
import {
  BASE_PRICE_SLUG,
  BOOKING_RANGE_SOURCE,
  BOUNDS_SOURCE,
  COUNTRY_SOURCE,
  PURPOSE_TYPES,
  SEARCH_PER_PAGE,
  STATE_SOURCE,
} from 'application/constants';
import { SEARCH_RESOURCE } from 'application/resources';
import { AxiosError } from 'axios';
import { useListContext } from 'contexts/ListContext';
import { getParsedFilters } from 'helpers/filters/getParsedFilters';
import { getParsedListParams } from 'helpers/getParsedListParams';
import { get, isEmpty } from 'lodash';
import { GetServerSidePropsContext } from 'next';
import { FilterAttributeRecord } from 'types/index';

import { getPolygonFilterFromBounds } from './helpers';

import { fetchFilterAttributes, useGetFilterAttributes } from '../useGetFilterAttributes';

// TODO: [RM-7602] Fix types
type SearchResultQueryKey = readonly [string, string, Record<string, unknown> | null];
export type SearchIndexDataType = typeof zodSearchSchemas.index._output[number];

interface SearchMeta {
  filterAttributes?: FilterAttributeRecord[];
  context?: GetServerSidePropsContext;
}

// TODO: [RM-7602] Fix types
const getQueryKey = (listParams?: Record<string, unknown>): SearchResultQueryKey => {
  // TODO: RM-7218 Investigate how to improve ListContext to not use unnecessary filters from query
  return [SEARCH_RESOURCE, 'getList', listParams || null];
};

const defaultSort = {};

const queryFn: QueryFunction<ListResponseType<SearchIndexDataType>, SearchResultQueryKey> = async (
  params,
) => {
  const { queryKey, pageParam, meta, signal } = params;

  const [pathname, , listParams] = queryKey;

  const { context } = meta as SearchMeta;

  // TODO: [RM-7602] Fix types
  const {
    bounds = null,
    booking_range,
    country,
    state,
    ...omitedFilters
  } = (listParams?.filter || {}) as ReturnType<typeof getParsedFilters>['selectedFilters'];

  const res = await fetchClient.get(
    {
      pathname,
      ...listParams,
      filter: {
        ...omitedFilters,
        ...booking_range,
        state_code: state,
        country_code: country,
        [BASE_PRICE_SLUG]: {
          min: get(omitedFilters, [BASE_PRICE_SLUG, 0]),
          max: get(omitedFilters, [BASE_PRICE_SLUG, 1]),
        },
        ...getPolygonFilterFromBounds({ bounds }),
      },
      page: pageParam,
      // TODO: [RM-7602] Fix types
      // @ts-expect-error refactor
      perPage: listParams?.perPage || SEARCH_PER_PAGE,
      sort: listParams?.sort || defaultSort,
    },
    { context, signal },
  );

  return {
    ...zodListResponseSchema.parse(res.data),
    data: zodSearchSchemas.index.parse(res.data.data),
  };
};

export const prefetchSearchResults = async (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  try {
    const listParams = getParsedListParams(context.query, {
      filter: {},
      sort: {},
      page: 1,
      perPage: SEARCH_PER_PAGE,
    });

    const filterAttributes = await fetchFilterAttributes(queryClient, context);

    const { selectedFilters } = getParsedFilters({
      filter: listParams?.filter || {},
      filterAttributes: filterAttributes || [],
      independentFiltersSlugs: [BOOKING_RANGE_SOURCE, BOUNDS_SOURCE, COUNTRY_SOURCE, STATE_SOURCE],
    });

    // TODO: [RM-7602] should be already paresed and valid
    const validListParams = {
      ...listParams,
      filter: selectedFilters,
    } as Record<string, unknown>;

    return queryClient.prefetchInfiniteQuery({
      queryKey: getQueryKey(validListParams),
      queryFn,
      getNextPageParam: () => 1,
      meta: { context },
    });
  } catch (err) {
    // fetchFilterAttributes can throw an error
  }
};

export const useGetSearchResults = (
  options?: Omit<
    UseInfiniteQueryOptions<
      ListResponseType<SearchIndexDataType>,
      AxiosError,
      ListResponseType<SearchIndexDataType>,
      ListResponseType<SearchIndexDataType>,
      SearchResultQueryKey
    >,
    'queryKey' | 'queryFn' | 'getNextPageParam'
  >,
  additionalListParams?: Partial<ListParams>,
) => {
  const { listParams } = useListContext();

  const { data: filterAttributes } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  const { selectedFilters } = getParsedFilters({
    filter: listParams?.filter || {},
    filterAttributes: filterAttributes || [],
    independentFiltersSlugs: [BOOKING_RANGE_SOURCE, BOUNDS_SOURCE, COUNTRY_SOURCE, STATE_SOURCE],
  });

  // TODO: [RM-7602] should be already paresed and valid
  const validListParams = {
    ...listParams,
    ...additionalListParams,
    filter: {
      ...additionalListParams?.filter,
      ...selectedFilters,
    },
  } as Record<string, unknown>;

  const { data, ...rest } = useInfiniteQuery<
    ListResponseType<SearchIndexDataType>,
    AxiosError,
    ListResponseType<SearchIndexDataType>,
    SearchResultQueryKey
  >({
    queryKey: getQueryKey(validListParams),
    queryFn,
    refetchOnMount: false,
    getNextPageParam: (lastPage) => {
      if (lastPage.links.next) {
        return lastPage.meta.current_page + 1;
      }
    },
    enabled: !isEmpty(filterAttributes),
    ...options,
  });

  return {
    ...rest,
    data,
    total: data?.pages[0] && data.pages[0].meta?.total,
  };
};
