import { CHECKIN_AT_SOURCE, CHECKOUT_AT_SOURCE } from 'application/constants';

import { bookingRangeFilterValueSchema, BookingRangeFilterValueType } from '../../schemas';
import { ParsedFilterResult, ParseFilterParams } from '../types';

export type ParseBookingRangeFilterValueParams<DefaultValue = BookingRangeFilterValueType> =
  ParseFilterParams<DefaultValue>;

export type ParseBookingRangeFilterValueReturn<DefaultValue = BookingRangeFilterValueType> =
  ParsedFilterResult<BookingRangeFilterValueType, DefaultValue>;

export const parseBookingRangeFilterValue = <DefaultValue = BookingRangeFilterValueType>(
  params: ParseBookingRangeFilterValueParams<DefaultValue>,
): ParseBookingRangeFilterValueReturn<DefaultValue> => {
  const defaultValues: BookingRangeFilterValueType = {
    [CHECKIN_AT_SOURCE]: '',
    [CHECKOUT_AT_SOURCE]: '',
  };

  const { filter, defaultValue = defaultValues as DefaultValue } = params;

  const parseResult = bookingRangeFilterValueSchema.safeParse(filter);

  if (parseResult.success) {
    return {
      isEmpty: false,
      value: parseResult.data,
      defaultValue,
    };
  }

  return {
    isEmpty: true,
    value: defaultValue,
    defaultValue,
  };
};
