import { fetchClient, zodFilterAttributeResourceIndexSchema } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import {
  BASE_PRICE_SLUG,
  FILTER_ATTRIBUTE_TYPEABLE_TYPES,
  PURPOSE_SOURCE,
  PURPOSE_TYPES,
  PurposeType,
  TYPEABLE_ID_SOURCE,
  TYPEABLE_TYPE_SOURCE,
} from 'application/constants';
import { ATTRIBUTES_RESOURCE } from 'application/resources';
import { useCurrencyContext } from 'contexts/CurrencyContext';
import { getPriceInCurrency } from 'helpers/getPriceInCurrency';
import { CURRENCY_QUERY_PARAM_KEY } from 'hooks/useCurrencyController';
import get from 'lodash/get';
import { GetServerSidePropsContext } from 'next';
import { CurrencyRecord, DealTypeRecord, FilterAttributeRecord } from 'types/index';

import { fetchCurrencies, useGetCurrencies } from '../useGetCurrencies';
import { fetchDealTypes, useGetDealTypes } from '../useGetDealTypes';

const INQUIRY_SHORT_TERM_SLUG = 'inquiry-short-term';

export interface UseGetFilterAttributesParams {
  [PURPOSE_SOURCE]: PurposeType;
}

export interface GetQueryKeyParams {
  [PURPOSE_SOURCE]: PurposeType;
  dealTypes?: DealTypeRecord[];
}

// TODO: check if BE can do this convertion
const getConvertedToCurrencyFilterAttributes = (params: {
  filterAttributes: FilterAttributeRecord[];
  currencies: CurrencyRecord[];
  currencyCode?: string;
}) => {
  const { filterAttributes, currencies, currencyCode } = params;

  const currentCurrency = currencies.find((currency) => {
    return currency.code === currencyCode;
  });

  return filterAttributes.map((fa) => {
    const { slug, min, max, currency } = fa.filter_attribute;

    const getConvertedValue = (params: { value: number; isMin?: boolean }) => {
      const { value, isMin = false } = params;
      return typeof currency?.code === 'undefined'
        ? isMin
          ? min
          : max
        : /* Use rounding to avoid many decimals and to get valid value between min/max */
          Math[isMin ? 'ceil' : 'floor'](
            getPriceInCurrency({
              amount: value,
              currentCurrencyCode: currencyCode,
              initialCurrencyCode: currency.code,
              currencies,
            }),
          );
    };
    if (slug === BASE_PRICE_SLUG) {
      return {
        ...fa,
        filter_attribute: {
          ...fa.filter_attribute,
          ...(typeof min === 'number'
            ? {
                min: getConvertedValue({ value: min, isMin: true }),
              }
            : {}),
          ...(typeof max === 'number'
            ? {
                max: getConvertedValue({ value: max, isMin: false }),
              }
            : {}),
          currency: currentCurrency,
        },
      };
    }
    return fa;
  });
};

const getQueryKey = ({ purpose, dealTypes }: GetQueryKeyParams) => {
  const selectedDealType = dealTypes?.find((el) => el.slug === INQUIRY_SHORT_TERM_SLUG);
  const typeableId = get(selectedDealType, 'id') || '';

  return [
    ATTRIBUTES_RESOURCE,
    'list',
    {
      purpose,
      typeableId,
    },
  ] as const;
};

type FilterAttributesQueryKey = ReturnType<typeof getQueryKey>;

const queryFn: QueryFunction<FilterAttributeRecord[], FilterAttributesQueryKey> = async (
  params,
) => {
  const { queryKey, meta } = params;
  const [, , { purpose, typeableId }] = queryKey;
  const context = meta?.context as GetServerSidePropsContext;

  const filters = {
    [PURPOSE_SOURCE]: purpose,
    [TYPEABLE_ID_SOURCE]: typeableId,
    [TYPEABLE_TYPE_SOURCE]: FILTER_ATTRIBUTE_TYPEABLE_TYPES['deal_type'],
  };

  const {
    data: { data },
  } = await fetchClient.get(
    {
      pathname: ATTRIBUTES_RESOURCE,
      filter: filters,
    },
    { context },
  );

  return zodFilterAttributeResourceIndexSchema.parse(data);
};

export const prefetchFilterAttributes = async (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  try {
    const purpose = PURPOSE_TYPES['searching'];
    const dealTypes = await fetchDealTypes(queryClient, context);

    return queryClient.prefetchQuery({
      queryKey: getQueryKey({ purpose, dealTypes }),
      queryFn,
      meta: { context },
    });
  } catch (err) {
    // fetchDealTypes can throw error
  }
};

export const fetchFilterAttributes = async (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  try {
    const currencyCode = get(context.query, CURRENCY_QUERY_PARAM_KEY) as string;

    const purpose = PURPOSE_TYPES['searching'];
    const dealTypes = await fetchDealTypes(queryClient, context);
    const currencies = await fetchCurrencies(queryClient, context);

    const data = await queryClient.fetchQuery({
      queryKey: getQueryKey({ purpose, dealTypes }),
      queryFn,
      meta: { context },
    });

    return getConvertedToCurrencyFilterAttributes({
      filterAttributes: data,
      currencies,
      currencyCode,
    });
  } catch (err) {
    // fetchDealTypes can throw error
  }
};

export const useGetFilterAttributes = (params: UseGetFilterAttributesParams) => {
  const { purpose } = params;

  const { data: dealTypes } = useGetDealTypes();
  const { data: currencies = [] } = useGetCurrencies();
  const { currency } = useCurrencyContext();

  const result = useQuery({
    queryKey: getQueryKey({ purpose, dealTypes }),
    queryFn,
    refetchOnMount: false,
    enabled: !!purpose && !!dealTypes,
  });

  if (!result.data) return result;

  const convertedData = getConvertedToCurrencyFilterAttributes({
    currencies,
    currencyCode: currency?.code,
    filterAttributes: result.data,
  });

  return { ...result, data: convertedData };
};
