import { IndependentFiltersSlugs } from '../../types';
import {
  formatFilterAttributesFilterValuesToURL,
  FormatFilterAttributesFilterValuesToURLParams,
  FormatFilterAttributesFilterValuesToURLReturn,
} from '../formatFilterAttributesFilterValuesToURL';
import {
  formatIndependentFilterValuesToURL,
  FormatIndependentFilterValuesToURLReturn,
} from '../formatIndependentFilterValuesToURL';

export interface FormatFilterValuesParams<Slugs>
  extends FormatFilterAttributesFilterValuesToURLParams {
  independentFiltersSlugs?: Slugs;
}

export type FormatFilterValuesReturn<Slugs extends IndependentFiltersSlugs> =
  FormatFilterAttributesFilterValuesToURLReturn & FormatIndependentFilterValuesToURLReturn<Slugs>;

export const formatFilterValues = <Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs>(
  params: FormatFilterValuesParams<Slugs>,
): FormatFilterValuesReturn<Slugs> => {
  const { values, filterAttributes, independentFiltersSlugs } = params;

  const formattedFilterAttributesFilterValues = formatFilterAttributesFilterValuesToURL({
    filterAttributes,
    values,
  });

  const formattedIndependentFilterValues = formatIndependentFilterValuesToURL({
    values,
    slugs: independentFiltersSlugs,
  });

  return {
    ...formattedFilterAttributesFilterValues,
    ...formattedIndependentFilterValues,
  };
};
