import { useCallback } from 'react';

import { PURPOSE_TYPES } from 'application/constants';
import { useListContext } from 'contexts/ListContext';
import {
  formatFilterValues,
  getParsedFilters,
  GetParsedFiltersParams,
  GetParsedFiltersReturn,
  IndependentFiltersDefaultValues,
} from 'helpers/filters';
import { IndependentFiltersSlugs } from 'helpers/filters/types';
import { useGetFilterAttributes } from 'hooks/queries';
import { ListControllerMethodOptions } from 'hooks/useListController';
import { get } from 'lodash';

export type UseFiltersParams<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown>,
> = Pick<
  GetParsedFiltersParams<Slugs, DefaultValues>,
  'includeFilterAttribute' | 'independentFiltersSlugs' | 'defaultValues'
>;

export interface UseFiltersReturn<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
> extends GetParsedFiltersReturn<Slugs, DefaultValues> {
  addFilter: (key: string, value: unknown, options?: ListControllerMethodOptions) => void;
  removeFilter: (key: string, options?: ListControllerMethodOptions) => void;
  setFilters: (
    callback: (
      prevFilters: GetParsedFiltersReturn<Slugs, DefaultValues>['allFilters'],
    ) => Record<string, unknown>,
    options?: ListControllerMethodOptions,
  ) => void;
}

export const useFilters = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
>(
  params: UseFiltersParams<Slugs, DefaultValues> = {},
): UseFiltersReturn<Slugs, DefaultValues> => {
  const { includeFilterAttribute, independentFiltersSlugs, defaultValues } = params;

  const {
    listParams: { filter },
    setFilters,
    removeFilter,
  } = useListContext();

  const { data: filterAttributes = [] } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  const {
    allFilters,
    emptyFilters,
    selectedFilters,
    defaultValues: filtersDefaultValues,
    filterAttributes: filteredFilterAttributes,
  } = getParsedFilters({
    filter,
    filterAttributes,
    independentFiltersSlugs,
    defaultValues,
    includeFilterAttribute,
  });

  const addFormattedFilter = useCallback<UseFiltersReturn<Slugs, DefaultValues>['addFilter']>(
    (key, value, options) => {
      const formattedFilters = formatFilterValues({
        values: { [key]: value },
        filterAttributes,
        independentFiltersSlugs,
      });

      setFilters((prev) => ({ ...prev, ...formattedFilters }), options);
    },
    [filterAttributes, independentFiltersSlugs, setFilters],
  );

  const setFormattedFilters = useCallback<UseFiltersReturn<Slugs, DefaultValues>['setFilters']>(
    (callback, options) => {
      setFilters((prevFilters) => {
        const { allFilters } = getParsedFilters({
          filter: prevFilters,
          filterAttributes,
          independentFiltersSlugs,
          defaultValues,
          includeFilterAttribute,
        });

        const values = callback(allFilters);

        const formattedFilters = formatFilterValues({
          values,
          filterAttributes,
          independentFiltersSlugs,
        });

        return { ...prevFilters, ...formattedFilters };
      }, options);
    },
    [defaultValues, filterAttributes, includeFilterAttribute, independentFiltersSlugs, setFilters],
  );

  const removeFormattedFilter = useCallback<UseFiltersReturn<Slugs, DefaultValues>['removeFilter']>(
    (key, options) => {
      if (key in selectedFilters) {
        const formattedFilter = formatFilterValues({
          filterAttributes,
          values: {
            [key]: get(selectedFilters, key),
          },
        });

        Object.keys(formattedFilter).forEach((key) => {
          // TODO: RM-6605 Check behavior after parser improvement
          if (typeof formattedFilter[key] !== 'undefined') {
            removeFilter(key, options);
          }
        });
      }
    },
    [filterAttributes, removeFilter, selectedFilters],
  );

  return {
    allFilters,
    emptyFilters,
    selectedFilters,
    defaultValues: filtersDefaultValues,
    filterAttributes: filteredFilterAttributes,
    addFilter: addFormattedFilter,
    setFilters: setFormattedFilters,
    removeFilter: removeFormattedFilter,
  };
};
