import { createContext, FC, ReactNode, useCallback, useContext, useState } from 'react';

import { setCookie as setRealCookie } from 'cookies-next';

export interface CookiesContextType {
  cookies: Record<string, string | undefined>;
  setCookie: (name: string, value: string) => void;
}

export interface CookiesContextProviderProps {
  children: ReactNode;
  initialCookies: CookiesContextType['cookies'] | undefined;
}

export const CookiesContext = createContext<CookiesContextType>({
  cookies: {},
  setCookie: () => undefined,
});

export const CookiesContextProvider: FC<CookiesContextProviderProps> = (props) => {
  const { children, initialCookies } = props;

  const [cookies, setCookies] = useState<CookiesContextType['cookies']>(initialCookies || {});

  const setCookie = useCallback<CookiesContextType['setCookie']>((name, value) => {
    setCookies((prevCookies) => ({
      ...prevCookies,
      [name]: value,
    }));
    setRealCookie(name, value);
  }, []);

  return (
    <CookiesContext.Provider value={{ cookies, setCookie }}>{children}</CookiesContext.Provider>
  );
};

export const useCookiesContext = () => {
  return useContext(CookiesContext);
};
