import { z } from 'zod';

import { zodFileSchema } from './files';
import { zodIdSchema } from './general';

export const zodPageSchema = z.object({
  id: zodIdSchema,
  path: z.string(),
  meta_title: z.string(),
  meta_keywords: z.string().optional(),
  meta_description: z.string().optional(),
  content_files: z.array(zodFileSchema),
  // we don't have content on search page
  content: z.string().optional(),
});
