import { IndependentFiltersSlugs } from '../../types';
import {
  parseFilterAttributesFilterValues,
  ParseFilterAttributesFilterValuesParams,
  ParseFilterAttributesFilterValuesReturn,
} from '../parseFilterAttributesFilterValues';
import {
  IndependentFiltersDefaultValues,
  parseIndependentFilterValues,
  ParseIndependentFilterValuesReturn,
} from '../parseIndependentFilterValues';

export interface ParseFilterValuesParams<Slugs, DefaultValues>
  extends Omit<ParseFilterAttributesFilterValuesParams, 'defaultValues'> {
  independentFiltersSlugs?: Slugs;
  defaultValues?: DefaultValues;
}

export type ParseFilterValuesReturn<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown>,
> = {
  filters: ParseFilterAttributesFilterValuesReturn['filters'] &
    ParseIndependentFilterValuesReturn<Slugs, DefaultValues>['filters'];
  filterAttributes: ParseFilterAttributesFilterValuesReturn['filterAttributes'];
};

export const parseFilterValues = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
>(
  params: ParseFilterValuesParams<Slugs, DefaultValues>,
): ParseFilterValuesReturn<Slugs, DefaultValues> => {
  const {
    filter,
    filterAttributes,
    includeFilterAttribute,
    independentFiltersSlugs,
    defaultValues,
  } = params;

  const {
    filters: parsedFilterAttributesFilterValues,
    filterAttributes: filteredFilterAttributes,
  } = parseFilterAttributesFilterValues({
    filterAttributes,
    includeFilterAttribute,
    filter,
    defaultValues,
  });

  const { filters: parsedIndependentFilterValues } = parseIndependentFilterValues({
    filter,
    slugs: independentFiltersSlugs,
    defaultValues,
  });

  return {
    filters: {
      ...parsedFilterAttributesFilterValues,
      ...parsedIndependentFilterValues,
    },
    filterAttributes: filteredFilterAttributes,
  };
};
