import { parseMultipleNumericFilterValue } from 'helpers/filters/parsers';

import { GetNumericFilterValueSchemaParams } from '../../schemas';
import { FormatFilterAttributeToURLParams } from '../types';

type FormatMultipleNumericFilterValueToURLParams = FormatFilterAttributeToURLParams &
  GetNumericFilterValueSchemaParams;

type FormatMultipleNumericFilterValueToURLReturn = string | undefined;

export const formatMultipleNumericFilterValueToURL = (
  params: FormatMultipleNumericFilterValueToURLParams,
): FormatMultipleNumericFilterValueToURLReturn => {
  const { values, slug, min, max, step } = params;

  const parseResult = parseMultipleNumericFilterValue({
    filter: values,
    slug,
    min,
    max,
    step,
  });

  if (parseResult.isEmpty) {
    return undefined;
  }

  return parseResult.value.join(',');
};
