import { z } from 'zod';

import { zodIdSchema } from './general';

export const zodFileSchema = z
  .object({
    id: zodIdSchema,
    src: z.string(),
    title: z.string().optional().default(''),
  })
  .merge(
    z
      .object({
        mime_type: z.string(),
        collection: z.string(),
        name: z.string(),
      })
      .deepPartial(),
  );
