// eslint-disable-next-line @typescript-eslint/no-unused-vars
import React, { ReactNode } from 'react';

import { ChoicesContext, ChoicesContextValueType } from './ChoicesContext';

export interface ChoicesContextProviderParams {
  children: ReactNode;
  value: ChoicesContextValueType;
}

export const ChoicesContextProvider = ({ children, value }: ChoicesContextProviderParams) => (
  <ChoicesContext.Provider value={value}>{children}</ChoicesContext.Provider>
);
