import { BOOKING_RANGE_SOURCE, CHECKIN_AT_SOURCE, CHECKOUT_AT_SOURCE } from 'application/constants';
import { parseBookingRangeFilterValue } from 'helpers/filters/parsers';
import { get } from 'lodash';

import { FormatFilterToURLParams } from '../types';

export type FormatBookingRangeFilterValueToURLParams = FormatFilterToURLParams;

export type FormatBookingRangeFilterValueToURLReturn = Record<string, string | undefined>;

export const formatBookingRangeFilterValueToURL = (
  params: FormatBookingRangeFilterValueToURLParams,
): FormatBookingRangeFilterValueToURLReturn => {
  const { values } = params;

  const value = get(values, BOOKING_RANGE_SOURCE);

  const parseResult = parseBookingRangeFilterValue({
    filter: value,
  });

  if (parseResult.isEmpty) {
    return {
      [CHECKIN_AT_SOURCE]: undefined,
      [CHECKOUT_AT_SOURCE]: undefined,
    };
  }

  return parseResult.value;
};
