import { ListParams, WithRootProps } from '@packages/types';

export type NormalizeListParams = WithRootProps<
  Partial<ListParams>,
  {
    filter: unknown;
  }
>;

export const normalizeListParams = (params?: NormalizeListParams) => {
  if (!params) {
    return {};
  }

  const { sort, filter, page, perPage } = params;

  const normizedParams: Record<string, unknown> = {};

  if (page) {
    normizedParams['page'] = String(page || 1);
  }

  if (perPage) {
    normizedParams['per_page'] = String(perPage || 15);
  }

  if (filter) {
    normizedParams['filter'] = filter;
  }

  if (sort && sort.field) {
    normizedParams['sort'] = `${sort.order === 'ASC' ? '' : '-'}${sort.field}`;
  }

  return normizedParams;
};
