import {
  IndependentFiltersDefaultValues,
  parseFilterValues,
  ParseFilterValuesParams,
} from '../parsers';
import { splitParsedFilterValues, SplitParsedFilterValuesReturn } from '../splitParsedFilterValues';
import { IndependentFiltersSlugs } from '../types';

export type GetParsedFiltersParams<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown>,
> = ParseFilterValuesParams<Slugs, DefaultValues>;

export type GetParsedFiltersReturn<
  Slugs extends IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown>,
> = SplitParsedFilterValuesReturn<Slugs, DefaultValues> & {
  filterAttributes: GetParsedFiltersParams<Slugs, DefaultValues>['filterAttributes'];
};

export const getParsedFilters = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
  DefaultValues extends Partial<Record<Slugs[number], unknown>> & Record<string, unknown> = {
    [Key in Slugs[number]]: IndependentFiltersDefaultValues[Key];
  },
>(
  params: GetParsedFiltersParams<Slugs, DefaultValues>,
): GetParsedFiltersReturn<Slugs, DefaultValues> => {
  const { filters, filterAttributes } = parseFilterValues<Slugs, DefaultValues>(params);
  return { ...splitParsedFilterValues<Slugs, DefaultValues>({ filters }), filterAttributes };
};
