import { z, ZodTypeAny } from 'zod';

export type GetMultipleFilterValueSchemaReturn<Schema extends ZodTypeAny> = ReturnType<
  typeof getMultipleFilterValueSchema<Schema>
>;

export type MultipleFilterValueType<Schema extends ZodTypeAny> = z.output<
  GetMultipleFilterValueSchemaReturn<Schema>
>;

export const getMultipleFilterValueSchema = <Schema extends ZodTypeAny>(schema: Schema) => {
  return z.preprocess((value) => {
    if (typeof value === 'string') {
      return value.split(',');
    }

    return Array.isArray(value) ? value : [value];
  }, z.array(schema));
};
