import { z } from 'zod';

import { zodBooleanSchema, zodIdSchema } from './general';

export const zodCountryResourceSchema = z
  .object({
    id: zodIdSchema,
    title: z.string().optional().default(''),
  })
  .merge(
    z
      .object({
        code: z.string(),
        has_states: zodBooleanSchema,
        slug: z.string(),
        synonyms: z.array(z.string()),
        // 'en-US': { // TODO: https://king-david.atlassian.net/browse/RM-4112
        //   title: 'Israel',
        // },
      })
      .deepPartial(),
  );
