import { createContext } from 'react';

import { ListParams } from '@packages/types';
import { UseQueryResult } from '@tanstack/react-query';
import { noop } from 'lodash';

export type ChoicesContextChoiceType = Record<string, unknown>;

export interface ChoicesContextValueType<
  T extends ChoicesContextChoiceType = ChoicesContextChoiceType,
> extends Pick<UseQueryResult<T[], unknown>, 'error' | 'isFetching'> {
  name: string;
  allChoices: T[];
  availableChoices: T[];
  selectedChoices: T[];
  isFromReference: boolean;
  filter?: ListParams['filter'];
  sort?: ListParams['sort'];
  page?: ListParams['page'];
  perPage?: ListParams['perPage'];
  setFilter: (filters: ListParams['filter']) => void;
  setSort: (sort: ListParams['sort']) => void;
  setPage: (page: ListParams['page']) => void;
  setPerPage: (perPage: ListParams['perPage']) => void;
}

export const choicesContextDefaultValue: ChoicesContextValueType<ChoicesContextChoiceType> = {
  name: '',
  allChoices: [],
  availableChoices: [],
  selectedChoices: [],
  error: null,
  isFetching: false,
  isFromReference: false,
  page: 1,
  perPage: 15,
  setFilter: noop,
  setPage: noop,
  setPerPage: noop,
  setSort: noop,
};

export const ChoicesContext = createContext<ChoicesContextValueType>(choicesContextDefaultValue);
