import { zodReviewSchemas } from '@packages/providers';
import { ReviewItemType } from 'types/index';

export const getFormattedReviews = (
  reviews: typeof zodReviewSchemas.index._output,
): ReviewItemType[] => {
  return reviews.map((review) => {
    const { id, review_text, rating, added_at, user } = review;

    return {
      id,
      comment: review_text,
      rating,
      date: added_at,
      firstName: user?.first_name || '',
      avatarSrc: user?.avatar?.src || '',
    };
  });
};
