import { parseMultipleLabeledFilterValue } from 'helpers/filters/parsers';

import { FormatFilterAttributeToURLParams } from '../types';

type FormatMultipleLabeledFilterValueToURLParams = FormatFilterAttributeToURLParams;

type FormatMultipleLabeledFilterValueToURLReturn = string | undefined;

export const formatMultipleLabeledFilterValueToURL = (
  params: FormatMultipleLabeledFilterValueToURLParams,
): FormatMultipleLabeledFilterValueToURLReturn => {
  const { values, slug } = params;

  const parseResult = parseMultipleLabeledFilterValue({
    filter: values,
    slug,
  });

  if (parseResult.isEmpty) {
    return undefined;
  }

  return parseResult.value.join(',');
};
