import { isValid, parseISO } from 'date-fns';

import { GetIsConvertableToDateParam, GetIsConvertableToDateReturn } from './types';

const getIsConvertableToDate = (
  value: GetIsConvertableToDateParam,
): GetIsConvertableToDateReturn => {
  const preparedValue = typeof value === 'string' ? parseISO(value) : value;

  if (preparedValue && isValid(preparedValue)) {
    return true;
  }

  return false;
};

export default getIsConvertableToDate;
