import { FC, ReactNode } from 'react';

import { getSelectorsByUserAgent } from 'react-device-detect';

import { DeviceDetectContext } from './DeviceDetectContext';

interface DeviceDetectContextProviderProps {
  children: ReactNode;
  userAgent?: string;
}

const emptyObject = {};

export const DeviceDetectContextProvider: FC<DeviceDetectContextProviderProps> = (props) => {
  const { children, userAgent = '' } = props;

  const selectors = getSelectorsByUserAgent(userAgent);

  const res = selectors || emptyObject;

  return <DeviceDetectContext.Provider value={res}>{children}</DeviceDetectContext.Provider>;
};
