import { useEffect, useMemo, useRef } from 'react';

import { debounce, DebouncedFunc, DebounceSettings, isEqual } from 'lodash';

export const useDebouncedCallback = <T extends (...args: unknown[]) => unknown>(
  callback: T,
  wait?: number | undefined,
  options?: DebounceSettings,
): DebouncedFunc<T> => {
  const optionsRef = useRef(options);

  const memoizedOptions = isEqual(options, optionsRef.current) ? optionsRef.current : options;
  optionsRef.current = memoizedOptions;

  const debouncedCallback = useMemo(() => {
    return debounce(callback, wait, memoizedOptions);
  }, [callback, wait, memoizedOptions]);

  const debouncedCallbackRef = useRef(debouncedCallback);
  debouncedCallbackRef.current = debouncedCallback;

  useEffect(() => {
    return () => {
      debouncedCallbackRef.current.cancel();
    };
  }, []);

  return debouncedCallback as DebouncedFunc<T>;
};
