export type GetSelectNumericChoicesParams = {
  min: number;
  max: number;
  step: number;
  optionText?: string;
};

export type GetSelectNumericChoicesReturn = Array<{ value: number; title: string }>;

export const getSelectNumericChoices = (
  params: GetSelectNumericChoicesParams,
): GetSelectNumericChoicesReturn => {
  const { min, max, step } = params;
  if (min > max) return [];
  const length = (max - min) / step + 1;

  const resultChoices = Array.from({ length }, (_, i) => ({
    value: min + i * step,
    title: String(min + i * step),
  }));

  return resultChoices;
};
