import doubleConversion from 'helpers/doubleConversion';
import { CurrencyRecord } from 'types/index';

export interface GetPriceInCurrencyParams {
  amount: number;
  initialCurrencyCode?: string;
  currentCurrencyCode?: string;
  currencies?: CurrencyRecord[];
}

export type GetPriceInCurrencyReturn = number;

export const getPriceInCurrency = (params: GetPriceInCurrencyParams): GetPriceInCurrencyReturn => {
  const { amount, initialCurrencyCode, currentCurrencyCode, currencies } = params;

  const baseToAmountRate = currencies?.find((v) => v.code === initialCurrencyCode)?.rate || 0;

  const baseToResultRate = currencies?.find((v) => v.code === currentCurrencyCode)?.rate || 0;

  const priceValue = doubleConversion({
    amount,
    baseToAmountRate,
    baseToResultRate,
  });

  return Math.floor(priceValue * 1000) / 1000; // cut to 3 decimals
};
