import { useMemo } from 'react';

import {
  fetchClient,
  ListResponseType,
  zodListResponseSchema,
  zodReviewSchemas,
} from '@packages/providers';
import { Identifier } from '@packages/types';
import { QueryFunction, useInfiniteQuery } from '@tanstack/react-query';
import { REVIEWS_RESOURCE } from 'application/resources';
import { get } from 'lodash';
import { GetServerSidePropsContext } from 'next';
import { ReviewItemType } from 'types/index';

import { getFormattedReviews } from './helpers/getFormattedReviews';

const getQueryKey = ({ listingId }: { listingId?: Identifier }) => {
  return [REVIEWS_RESOURCE, 'getList', { listingId }] as const;
};

type ReviewsQueryKey = ReturnType<typeof getQueryKey>;

export interface UseGetInfiniteReviewsParams {
  listingId?: Identifier;
}

const queryFn: QueryFunction<ListResponseType<ReviewItemType>, ReviewsQueryKey> = async (
  params,
) => {
  const { queryKey, pageParam, meta } = params;

  const [pathname, , { listingId }] = queryKey;

  const context = meta?.context as GetServerSidePropsContext;

  const res = await fetchClient.get(
    {
      pathname,
      sort: { field: 'added_at', order: 'DESC' },
      filter: { ...(listingId && { listing_ids: [String(listingId)] }) },
      page: pageParam,
      perPage: 10,
    },
    {
      context,
    },
  );

  return {
    ...zodListResponseSchema.parse(res.data),
    data: getFormattedReviews(zodReviewSchemas.index.parse(res.data.data)),
  };
};

export const useGetInfiniteReviews = (params?: UseGetInfiniteReviewsParams) => {
  const { listingId } = params || {};

  const { data, ...rest } = useInfiniteQuery<
    ListResponseType<ReviewItemType>,
    unknown,
    ListResponseType<ReviewItemType>,
    ReviewsQueryKey
  >({
    queryKey: getQueryKey({ listingId }),
    queryFn,
    refetchOnMount: false,
    getNextPageParam: (lastPage) => {
      if (lastPage?.links?.next && lastPage?.meta) {
        return lastPage.meta.current_page + 1;
      }
    },
  });

  const flattenedData = useMemo(() => {
    if (!data) return;

    return data.pages.flatMap((page) => {
      return page.data;
    });
  }, [data]);

  return {
    ...rest,
    data: flattenedData,
    total: get(data, 'pages.0.meta.total'),
  };
};
