import { parseLabeledFilterValue } from 'helpers/filters/parsers';

import { FormatFilterAttributeToURLParams } from '../types';

export type FormatLabeledFilterValueToURLParams = FormatFilterAttributeToURLParams;

export type FormatLabeledFilterValueToURLReturn = string | undefined;

export const formatLabeledFilterValueToURL = (
  params: FormatLabeledFilterValueToURLParams,
): FormatLabeledFilterValueToURLReturn => {
  const { values, slug } = params;

  const parseResult = parseLabeledFilterValue({ filter: values, slug });

  return parseResult.isEmpty ? undefined : parseResult.value;
};
