import { isDate, isValid, parse } from 'date-fns';

import { GetParsedToDateValueParams, GetParsedToDateValueReturn } from './types';

const getParsedToDateValue = <Fallback = undefined>(
  params: GetParsedToDateValueParams<Fallback>,
): GetParsedToDateValueReturn<Fallback> => {
  const { value, format, fallbackValue } = params;

  if (!value) return fallbackValue as Fallback;

  try {
    if (typeof value === 'string') {
      const parsedDate = parse(value, format, new Date());

      if (isValid(parsedDate)) {
        return parsedDate;
      }
    } else if (isDate(value)) {
      if (isValid(value)) {
        return value as Date;
      }
    }
    throw Error;
  } catch {
    return fallbackValue as Fallback;
  }
};

export default getParsedToDateValue;
