import isValid from 'date-fns/isValid';
import { z } from 'zod';

export const zodIdSchema = z.union([z.string().min(1), z.number()]);

/**
 * Transforms possible 0 and 1 to boolean. Typisation shows `boolean | number`, after `.parse` we get `boolean` only
 */
export const zodBooleanSchema = z
  .union([z.boolean(), z.literal(1), z.literal(0)])
  .transform((v): boolean => {
    return !!v;
  });

export const zodDateSchema = z.string().transform((v: string) => {
  if (v && isValid(new Date(v))) return new Date(v);
});

export const zodFilterSchema = z.union([
  z.string(),
  z.number(),
  z.array(z.string()),
  z.array(z.number()),
]);

/** Helps to avoid issues, e.g when response passes number as string.
 * Returns type: number | undefined */
export const zodNumberSchema = z.union([z.string(), z.number()]).transform((v: string | number) => {
  const parsed = parseFloat('' + v);

  if (isNaN(parsed)) {
    // This is a special symbol you can use to
    // return early from the transform function.
    // It has type `never` so it does not affect the
    // inferred return type.
    return z.NEVER;
  }

  return parsed;
});

export const zodListResponseSchema = z.object({
  data: z.array(z.unknown()),
  links: z.object({
    next: z.string().nullish(),
    prev: z.string().nullish(),
  }),
  meta: z.object({
    from: z.number().nullable(),
    to: z.number().nullable(),
    current_page: z.number(),
    last_page: z.number(),
    total: z.number(),
  }),
});

type ListResponseBase = typeof zodListResponseSchema._output;
export type ListResponseType<T = unknown> = Omit<ListResponseBase, 'data'> & {
  data: T extends Record<string, unknown> ? T[] : ListResponseBase['data'];
};
