import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

import { zodFileSchema } from './files';
import { zodBooleanSchema, zodDateSchema, zodIdSchema, zodNumberSchema } from './general';

export const zodReviewItemSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    // partial
    z
      .object({
        rating: zodNumberSchema,
        firstName: z.string(),
        date: zodDateSchema,
        avatarSrc: z.string(),
        comment: z.string(),
        place: z.string(),
      })
      .deepPartial(),
  );

export const zodReviewItemsSchema = z.array(zodReviewItemSchema);

export const zodReviewsResourceSchema = z
  .object({
    id: zodIdSchema,
    review_text: z.string(),
    listing: z
      .object({
        slug: z.string(),
      })
      .merge(
        z
          .object({
            description: z.string(),
            title: z.string(),
            id: z.string(),
            address: z.string(),
          })
          .deepPartial(),
      ),
  })
  .merge(
    // partial
    z
      .object({
        listing_id: zodIdSchema,
        user_id: zodIdSchema,
        user: z.object({
          email: z.string(),
          first_name: z.string(),
          phone: z.string(),
          avatar: zodFileSchema,
        }),
        rating: zodNumberSchema,
        is_displayed: zodBooleanSchema,
        is_promoted: zodBooleanSchema,
        added_at: zodDateSchema,
      })
      .deepPartial(),
  );

export const zodReviewSchemas = {
  index: z.array(zodReviewsResourceSchema).default([]),
};
export type ReviewTypes = TypesFromSchemas<typeof zodReviewSchemas>;
