import { useEffect, useState } from 'react';

import { createPortal } from 'react-dom';

import { UsePortalParams, UsePortalReturn } from './types';

export const usePortal = (params: UsePortalParams): UsePortalReturn => {
  const { node, getContainer, intervalMs = 50, maxAttempts = 100 } = params;
  const [portalElement, setPortalElement] = useState<Parameters<typeof createPortal>[1] | null>(
    null,
  );

  useEffect(() => {
    let attemps = 0;
    const container = getContainer();

    // Set portal element if it was find immediatelly
    if (container && !portalElement) {
      setPortalElement(container);
      return;
    }

    // Remove portal element if it was disapeared
    if (!container && portalElement) {
      setPortalElement(null);
    }

    // Set portal element as fast as it was found
    const intervalId = setInterval(() => {
      const container = getContainer();
      attemps++;
      if (container) {
        clearInterval(intervalId);
        setPortalElement(container);
      }
      if (attemps > maxAttempts) {
        clearInterval(intervalId);
      }
    }, intervalMs);
  }, [getContainer, intervalMs, maxAttempts, portalElement]);

  return portalElement ? createPortal(node, portalElement) : null;
};

export default usePortal;
