import {
  BOUNDS_SOURCE,
  EAST_SOURCE,
  NORTH_SOURCE,
  SOUTH_SOURCE,
  WEST_SOURCE,
} from 'application/constants';
import { parseMapBoundsFilterValues } from 'helpers/filters/parsers';
import { get, mapValues } from 'lodash';

import { FormatFilterToURLParams } from '../types';

export type FormatMapBoundsFilterValueToURLParams = FormatFilterToURLParams;

export type FormatMapBoundsFilterValueToURLReturn = Record<string, string | undefined>;

export const formatMapBoundsFilterValueToURL = (
  params: FormatMapBoundsFilterValueToURLParams,
): FormatMapBoundsFilterValueToURLReturn => {
  const { values } = params;

  const value = get(values, BOUNDS_SOURCE);

  const parseResult = parseMapBoundsFilterValues({
    filter: value,
  });

  if (parseResult.isEmpty) {
    return {
      [WEST_SOURCE]: undefined,
      [EAST_SOURCE]: undefined,
      [SOUTH_SOURCE]: undefined,
      [NORTH_SOURCE]: undefined,
    };
  }

  return mapValues(parseResult.value, (value) => String(value));
};
