import { ComponentsOverrides } from '@mui/material';
import { ITheme } from 'theme/types';

export const SMALL_CHIP_HEIGHT = 24;

const getMuiChipOverrides = (): ComponentsOverrides<ITheme>['MuiChip'] => {
  return {
    root: {
      padding: 0,
      '&.Mui-disabled': {
        opacity: 1,
      },
    },
    sizeMedium: {
      height: 32,
    },
    sizeSmall: {
      height: SMALL_CHIP_HEIGHT,
    },

    filledPrimary: ({ theme }) => ({
      background: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
      ...theme.typography.body2,
      '&:hover, &.Mui-focusVisible': {
        background: theme.palette.primary.dark,
        boxShadow: 'none',
      },
      '&.MuiChip-sizeSmall': {
        ...theme.typography.small2,
      },
      '&&.Mui-disabled': {
        background: theme.palette.primary.border,
      },
    }),
    outlined: ({ theme }) => ({
      color: theme.palette.text.light,
      border: `1px solid ${theme.palette.other.border}`,
      background: theme.palette.background.default,
      ...theme.typography.body2,
      '&:hover, &.Mui-focusVisible': {
        background: theme.palette.background.default,
        color: theme.palette.text.primary,
        border: `1px solid ${theme.palette.text.primary}`,
      },
      '&.MuiChip-sizeSmall': {
        ...theme.typography.small2,
      },
      '&&.Mui-disabled': {
        background: theme.palette.background.default,
        color: theme.palette.primary.border,
        border: `1px solid ${theme.palette.primary.border}`,
      },
    }),

    deleteIcon: {
      color: 'currentColor',
      ':hover': {
        color: 'currentColor',
      },
    },
    deleteIconMedium: {
      width: 18,
      height: 18,
    },
    deleteIconSmall: {
      width: 14,
      height: 14,
    },

    icon: ({ theme }) => ({
      color: 'currentColor',
      marginRight: theme.spacing(-0.5),
      marginLeft: theme.spacing(0.5),
    }),
  };
};

export default getMuiChipOverrides;
