import { fetchClient, zodListingSchemas } from '@packages/providers';
import { Identifier } from '@packages/types';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { listingRoute } from 'application/constants';
import get from 'lodash/get';
import { GetServerSidePropsContext } from 'next';
import { useRouter } from 'next/router';

const getQueryKey = (id: string) => ['listings', id];

type ListingQueryKey = ReturnType<typeof getQueryKey>;

const queryFn: QueryFunction<typeof zodListingSchemas.show._output, ListingQueryKey> = async (
  params,
) => {
  const { queryKey, meta } = params;

  const [, listingIdorSlug] = queryKey;

  const context = meta?.context as GetServerSidePropsContext;

  const res = await fetchClient.get({ pathname: `listings/${listingIdorSlug}` }, { context });

  return zodListingSchemas.show.parse(res.data.data);
};

export const prefetchListing = (queryClient: QueryClient, context: GetServerSidePropsContext) => {
  const listingIdOrSlug = context.query?.[listingRoute.param];

  if (!listingIdOrSlug || typeof listingIdOrSlug !== 'string') return Promise.resolve();

  return queryClient.prefetchQuery({
    queryKey: getQueryKey(typeof listingIdOrSlug === 'string' ? listingIdOrSlug : ''),
    queryFn,
    meta: { context },
  });
};

export const useGetListing = (idOrSlug?: Identifier | string) => {
  const { query } = useRouter();

  const listingIdOrSlug = idOrSlug || get(query, listingRoute.param);

  return useQuery({
    queryKey: getQueryKey(typeof listingIdOrSlug === 'string' ? listingIdOrSlug : ''),
    queryFn,
    refetchOnMount: false,
    enabled: !!listingIdOrSlug,
  });
};
