import { getFormattedDateValue } from '@packages/helpers';
import {
  ACCOMMODATES_SOURCE,
  BOOKING_RANGE_SOURCE,
  CHECKIN_AT_SOURCE,
  CHECKOUT_AT_SOURCE,
} from 'application/constants';
import { addDays } from 'date-fns';
import { useFilters } from 'hooks/filters/useFilters';
import { get } from 'lodash';

export interface UseParsedMainURLFiltersReturn {
  [CHECKIN_AT_SOURCE]: string;
  [CHECKOUT_AT_SOURCE]: string;
  [ACCOMMODATES_SOURCE]: number;
}

export const useParsedMainURLFilters = (): UseParsedMainURLFiltersReturn => {
  const { allFilters } = useFilters({
    includeFilterAttribute: (slug) => slug === ACCOMMODATES_SOURCE,
    independentFiltersSlugs: [BOOKING_RANGE_SOURCE],
    defaultValues: {
      [BOOKING_RANGE_SOURCE]: {
        [CHECKIN_AT_SOURCE]: getFormattedDateValue({
          value: new Date(),
          format: 'yyyy-MM-dd',
          fallbackValue: '',
        }),
        [CHECKOUT_AT_SOURCE]: getFormattedDateValue({
          value: addDays(new Date(), 7),
          format: 'yyyy-MM-dd',
          fallbackValue: '',
        }),
        [ACCOMMODATES_SOURCE]: 1,
      },
    },
  });

  const checkin = allFilters[BOOKING_RANGE_SOURCE][CHECKIN_AT_SOURCE];
  const checkout = allFilters[BOOKING_RANGE_SOURCE][CHECKOUT_AT_SOURCE];
  const accommodates = get(allFilters, ACCOMMODATES_SOURCE);

  return {
    [CHECKIN_AT_SOURCE]: checkin,
    [CHECKOUT_AT_SOURCE]: checkout,
    [ACCOMMODATES_SOURCE]: typeof accommodates === 'number' ? accommodates : 1,
  };
};
