import {
  BOOKING_RANGE_SOURCE,
  BOUNDS_SOURCE,
  COUNTRY_SOURCE,
  STATE_SOURCE,
} from 'application/constants';

import { INDEPENDENT_FILTERS_SLUGS } from '../../constants';
import { IndependentFiltersSlugs } from '../../types';
import {
  formatBookingRangeFilterValueToURL,
  FormatBookingRangeFilterValueToURLReturn,
} from '../formatBookingRangeFilterValueToURL';
import {
  formatCountryFilterValueToURL,
  FormatCountryFilterValueToURLReturn,
} from '../formatCountryFilterValueToURL';
import {
  formatMapBoundsFilterValueToURL,
  FormatMapBoundsFilterValueToURLReturn,
} from '../formatMapBoundsFilterValueToURL';
import {
  formatStateFilterValueToURL,
  FormatStateFilterValueToURLReturn,
} from '../formatStateFilterValueToURL';

type AllIndependentFilters = {
  [BOUNDS_SOURCE]: FormatMapBoundsFilterValueToURLReturn;
  [BOOKING_RANGE_SOURCE]: FormatBookingRangeFilterValueToURLReturn;
  [COUNTRY_SOURCE]: FormatCountryFilterValueToURLReturn;
  [STATE_SOURCE]: FormatStateFilterValueToURLReturn;
};

export interface FormatIndependentFilterValuesToURLParams<Slugs extends IndependentFiltersSlugs> {
  values: Record<string, unknown>;
  slugs?: Slugs;
}

// Return only filters that are listed in `slugs` array
export type FormatIndependentFilterValuesToURLReturn<Slugs extends IndependentFiltersSlugs> = {
  [Key in keyof AllIndependentFilters as Key extends Slugs[number]
    ? Key
    : never]: AllIndependentFilters[Key];
};

export const formatIndependentFilterValuesToURL = <
  Slugs extends IndependentFiltersSlugs = IndependentFiltersSlugs,
>(
  params: FormatIndependentFilterValuesToURLParams<Slugs>,
): FormatIndependentFilterValuesToURLReturn<Slugs> => {
  const { values, slugs = INDEPENDENT_FILTERS_SLUGS } = params;

  return slugs.reduce((acc, slug) => {
    switch (slug) {
      case BOOKING_RANGE_SOURCE: {
        const formatResult = formatBookingRangeFilterValueToURL({ values });
        return { ...acc, ...formatResult };
      }
      case BOUNDS_SOURCE: {
        const formatResult = formatMapBoundsFilterValueToURL({ values });
        return { ...acc, ...formatResult };
      }
      case STATE_SOURCE: {
        return { ...acc, [slug]: formatStateFilterValueToURL({ values, slug }) };
      }
      case COUNTRY_SOURCE: {
        return { ...acc, [slug]: formatCountryFilterValueToURL({ values, slug }) };
      }
      default:
        return acc;
    }
  }, {} as FormatIndependentFilterValuesToURLReturn<Slugs>);
};
