import { z } from 'zod';

import { zodCompanySchema, zodNavigatorSchema } from './common';
import { zodFileSchema } from './files';
import { zodBooleanSchema, zodIdSchema } from './general';
import { zodLocaleSchemas } from './locales';

export const zodSiteShowSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        gtm_code: z.string(),
        map_key: z.string(),
        map_id: z.string(),
        phone: z.string(),
        is_exposed_to_search_engines: zodBooleanSchema,
        logo_landscape: zodFileSchema,
        fav_icon: zodFileSchema,
        color_primary: z.string(),
        color_secondary: z.string(),
        navigators_ids: z.array(zodIdSchema),
        navigators: z.array(zodNavigatorSchema),
        captcha_key: z.string(),
        company: zodCompanySchema,
        locales: zodLocaleSchemas.index,
        styles: z.object({
          font_family: z.string(),
          button_border_radius: z.string(),
        }),
      })
      .deepPartial(),
  );

export const zodSiteSchemas = {
  show: zodSiteShowSchema,
};
