import { MAX_RANGABLE_PREFIX, MIN_RANGABLE_PREFIX } from 'application/constants';
import { parseNumericRangableFilterValue } from 'helpers/filters/parsers';
import { get } from 'lodash';

import { GetNumericFilterValueSchemaParams } from '../../schemas';
import { FormatFilterAttributeToURLParams } from '../types';

type FormatNumericRangableFilterValueToURLParams = FormatFilterAttributeToURLParams &
  GetNumericFilterValueSchemaParams;

type FormatNumericRangableFilterValueToURLReturn = Record<string, string | undefined>;

export const formatNumericRangableFilterValueToURL = (
  params: FormatNumericRangableFilterValueToURLParams,
): FormatNumericRangableFilterValueToURLReturn => {
  const { values, slug, min, max, step } = params;

  const minKey = `${MIN_RANGABLE_PREFIX}${slug}`;
  const maxKey = `${MAX_RANGABLE_PREFIX}${slug}`;

  const parseResult = parseNumericRangableFilterValue({
    filter: {
      [minKey]: get(values, [slug, 0]),
      [maxKey]: get(values, [slug, 1]),
    },
    slug,
    min,
    max,
    step,
  });

  if (parseResult.isEmpty) {
    return {
      [minKey]: undefined,
      [maxKey]: undefined,
    };
  }

  const [resultMin, resultMax] = parseResult.value;

  const result = {
    [minKey]: min === resultMin || resultMin === null ? undefined : String(resultMin),
    [maxKey]: max === resultMax || resultMax === null ? undefined : String(resultMax),
  };

  return result;
};
