import { FC } from 'react';

import { Box, BoxProps, createTheme, styled } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { clsx } from 'clsx';
import { palette } from 'theme/palette';
import { typography } from 'theme/typography';

// We need to create new theme here to let this component render/work until the main theme is initialized
const theme = createTheme({
  typography,
  palette,
});

export type IcoMoonProps = StyledRootProps & {
  /**
   * Pass name of icomoon without 'icon' prefix
   */
  icon: string;
  color?: BoxProps['color'];
  size?: 'large' | 'medium' | 'small' | 'extraSmall';
};

const PREFIX = 'IcoMoon';

const StyledIcoMoon = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
  shouldForwardProp: (p) => {
    return p !== 'color' && p !== 'size';
  },
})<{ color: IcoMoonProps['color']; size: IcoMoonProps['size'] }>(({ color, size }) => {
  const getSizeValue = (size: IcoMoonProps['size']) => {
    switch (size) {
      case 'extraSmall':
        return theme.typography.pxToRem(16);
      case 'small':
        return theme.typography.pxToRem(20);
      case 'medium':
        return theme.typography.pxToRem(24);
      case 'large':
        return theme.typography.pxToRem(35);
      default:
        return theme.typography.pxToRem(24);
    }
  };

  return {
    fontSize: getSizeValue(size),
    '&::before': {
      color,
    },
  };
});

export const IcoMoon: FC<IcoMoonProps> = (props) => {
  const { icon, color = theme.palette.text.primary, sx, className = '', size = 'medium' } = props;

  const classNames = clsx([`icon-${icon}`, className]);

  return <StyledIcoMoon component="i" className={classNames} size={size} color={color} sx={sx} />;
};
